.tb__uploader {
  position: relative;
  min-height: 300px;

  &__dropzone {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    background: rgba($tertiary, 0.3);
    border: 2px dashed rgba($tertiary, 0.8);
    border-radius: $border-radius;
    opacity: 0;
    transition: all 0.3s;
    pointer-events: none;
  }
  &--active {
  }
  &--active &__dropzone {
    opacity: 1;
    pointer-events: none;
  }
}