.tb__gallery {
  display: flex;
  justify-content: center;
  align-items: flex-start;

  img {
    display: block;
    flex: 0 0 auto;
    width: auto;
    height: auto;
    max-width:  100%;
    max-height: 100%;
    pointer-events: none;
  }
}

// Thumbs direction

.tb__gallery {
  &--horizontal {
    flex-wrap: wrap;

    .tb__gallery__thumbs {
      display:     flex;
      flex:        1 1 auto;
      align-items: flex-start;
      max-width:   100%;
      margin-top:  20px;
    }
    .tb__gallery__thumbs > img {
      flex:      1 1 auto;
      width:     100%;
      height:    auto;
      min-width: 0px;
    }
    .tb__gallery__thumbs > img:not(:first-child) {
      margin-left: 20px;
    }
    .swiper-slides,
    .swiper-thumbs {
      width: 100%;
    }
    .swiper-container {
      flex: 1 1 auto;
    }
    .swiper-thumbs:not(.swiper-active) .swiper-slide ~ .swiper-slide {
      margin-left: 20px;
    }

    & .swiper-thumbs:not(.swiper-active) .swiper-slide,
    & .tb__gallery__thumbs > img {
      max-width: calc(25.00% - 15.00px);
    }
    &[data-thumbs-number="3"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="3"] .tb__gallery__thumbs > img {
      max-width: calc(33.33% - 13.33px);
    }
    &[data-thumbs-number="4"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="4"] .tb__gallery__thumbs > img {
      max-width: calc(25.00% - 15.00px);
    }
    &[data-thumbs-number="5"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="5"] .tb__gallery__thumbs > img {
      max-width: calc(20.00% - 16.00px);
    }
    &[data-thumbs-number="6"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="6"] .tb__gallery__thumbs > img {
      max-width: calc(16.66% - 16.66px);
    }
    &[data-thumbs-number="7"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="7"] .tb__gallery__thumbs > img {
      max-width: calc(14.28% - 17.14px);
    }
    &[data-thumbs-number="8"] .swiper-thumbs:not(.swiper-active) .swiper-slide, &[data-thumbs-number="8"] .tb__gallery__thumbs > img {
      max-width: calc(12.50% - 17.50px);
    }
  }
  &--vertical {
    > .tb__gallery__slide,
    > .swiper-slides {
      flex:       1 1 auto;
      width:      100%;
      height:     auto;
      max-height: 100%;
      min-width:  0px;
    }
    .tb__gallery__thumbs,
    .tb__gallery__thumbs .swiper-container,
    .tb__gallery__thumbs .swiper-wrapper {
      display:        flex;
      flex-direction: column;
    }
    .tb__gallery__slide,
    .swiper-slides {
      order: 1;
    }
    .tb__gallery__thumbs {
      flex:       0 1 auto;
      align-self: stretch;
    }
    .tb__gallery__thumbs .swiper-container {
      flex: 1 1 0px;
    }
    .tb__gallery__thumbs .swiper-slide {
      display:         flex;
      align-items:     center;
      align-content:   center;
      justify-content: center;
      transition:      all 0.3s;
    }
    .tb__gallery__thumbs .swiper-container:not(.swiper-active) .swiper-slide {
      height: auto;
      flex:   1 1 0px;
    }
    .tb__gallery__thumbs > img:first-child ~ img,
    .tb__gallery__thumbs .swiper-container:not(.swiper-active) .swiper-slide + .swiper-slide {
      margin-top: 20px;
    }
    &[data-thumbs-number="3"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(3) ~ .swiper-slide,
    &[data-thumbs-number="4"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(4) ~ .swiper-slide,
    &[data-thumbs-number="5"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(5) ~ .swiper-slide,
    &[data-thumbs-number="6"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(6) ~ .swiper-slide,
    &[data-thumbs-number="7"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(7) ~ .swiper-slide,
    &[data-thumbs-number="8"] .swiper-container:not(.swiper-active) .swiper-slide:nth-child(8) ~ .swiper-slide {
      flex:   0 1 0px;
      height: 0;
      margin: 0;
    }
  }
  &__thumbs--right {
    order:       2;
    margin-left: 20px;
  }
  &__thumbs--left {
    order:        0;
    margin-right: 20px;
  }
}

// Before init

.tb__gallery {
  .swiper-slide {
    display: flex;
    align-self: stretch;
    align-items: center;
    align-content: center;
    justify-content: center;
    height: auto;
  }
  .swiper-slide .image img {
    transform: none;
  }
  .swiper-thumbs .swiper-slide {
    cursor: pointer;
    transition: opacity 0.3s;
  }
  .swiper-thumbs .swiper-slide:not(.active):not(:hover) {
    opacity: 0.5;
  }
  &:not([data-thumbs-number]) &__thumbs > img:nth-child(4) ~ img,
  &[data-thumbs-number="3"] &__thumbs > img:nth-child(3) ~ img,
  &[data-thumbs-number="4"] &__thumbs > img:nth-child(4) ~ img,
  &[data-thumbs-number="5"] &__thumbs > img:nth-child(5) ~ img,
  &[data-thumbs-number="6"] &__thumbs > img:nth-child(6) ~ img,
  &[data-thumbs-number="7"] &__thumbs > img:nth-child(7) ~ img,
  &[data-thumbs-number="8"] &__thumbs > img:nth-child(8) ~ img
  {
    display: none;
  }
}

// Preloader

.tb__gallery {
  .swiper-slide .tb__loading__bar {
    transition-delay: 2s;
  }
  .swiper-slide:not(.swiper-slide-active) .tb__loading__bar {
    opacity: 1;
  }
}