.tb__rating {
  position: relative;
  display: inline-flex;
  align-items: center;

  &__number {
    overflow: hidden;
    position: absolute !important;
    z-index: 1;
    top: 0;
    left: 0;
    justify-content: flex-start;

    svg {
      flex-shrink: 0;
    }
  }
  .btn-group {
    display: inline-flex;
    flex-direction: row-reverse;
    vertical-align: top;
  }
  .btn,
  .btn + .btn
  {
    height: auto;
    min-width: 0;
    margin: 0;
    padding: 0;
    border: none;
    color: inherit !important;
    background: transparent !important;
    opacity: 0;
    transition: all 0.2s;
    box-shadow: none;
  }
  .btn:hover,
  .btn:hover ~ .btn,
  .btn-group:not(:hover) .btn.active,
  .btn-group:not(:hover) .btn.active ~ .btn
  {
    opacity: 1;
  }
  .btn.btn.btn.btn:hover,
  .btn.btn.btn.btn.active,
  .btn.btn.btn.btn.active:hover
  {
    color: inherit !important;
  }
}
