// Unstyled table

.table-unstyled {
  width: 100%;

  > thead > tr > th,
  > thead > tr > td,
  > tbody > tr > th,
  > tbody > tr > td,
  > tfoot > tr > th,
  > tfoot > tr > td
  {
    padding: 0;
    border: none;
  }
}

// Vertically centered table

.table-align-middle {
  td {
    vertical-align: middle;
  }
}

// Table compare

.table-compare {
  th:first-child {
    position: sticky;
    z-index: 2;
    left: 0;
    width: 150px;
    min-width: 100px;
    max-width: 150px;
    background-color: #fff;
  }
  td {
    min-width: 150px;
    width: 200px;
  }
}

// Enhance

td[nowrap],
th[nowrap]
{
  white-space: nowrap;
}

// Table large

.table-lg th,
.table-lg td,
.tbody-lg th,
.tbody-lg td
{
  padding-top:    $table-cell-padding-lg;
  padding-bottom: $table-cell-padding-lg;
}

// Table nopadding

.table-nopadding {
  th:first-child,
  td:first-child
  {
    padding-left: 0;
  }
  th:last-child,
  td:last-child
  {
    padding-right: 0;
  }
}

.table-input.table-sm {
  td,
  th
  {
    padding-right: $input-btn-padding-x-sm;
    padding-left: $input-btn-padding-x-sm;
  }

  tfoot td {
    border: none;
  }

  .tr--input {
    td {
      padding: 0;

      &:not(:last-child) {
        border-right: 1px solid $table-border-color;

        .form-control,
        .custom-select
        {
          margin-right: -1px;
        }
      }
      .form-control,
      .custom-select
      {
        margin-left: -1px;
      }
    }
    .form-control,
    .custom-select
    {
      width: calc(100% + 2px);
      height: 44px;
      margin: -1px 0;
      border-radius: 0;
    }

    .custom-toggle .custom-control-label,
    .custom-toggle .custom-control-label::before
    {
      border-radius: 16px;
    }

    .input-group {
      flex-wrap: nowrap;
      margin-top: -1px;
      margin-bottom: -1px;
      border-radius: 0;

      .form-control {
        flex: 1 1 0;
        width: auto;
        margin-right: 0 !important;
        padding-right: 5px;
      }

      *[class][class]:after {
        width: calc(100% + 2px);
        height: calc(100% + 2px);
        margin-right: -1px;
        margin-top: -1px;
        border-radius: 0 !important;
      }
    }
  }

  .tr--remove {
    td {
      text-decoration: line-through;
    }
  }

  tfoot .tr--template {
    display: none !important;
  }

  .form-control.is-filled,
  .custom-select.is-filled,
  .input-group.is-filled .form-control,
  .input-group.is-filled .form-control ~ .input-group-prepend .input-group-text,
  .input-group.is-filled .form-control ~ .input-group-append .input-group-text
  {
    background-color: transparent;
  }
}

@include media-breakpoint-down(lg) {
  .table-input {
    table,
    tbody,
    tfoot,
    tr,
    th,
    td
    {
      display: flex;
      flex-wrap: wrap;
      border: 0;
      background-color: transparent;
    }

    thead {
      display: none;
    }

    tr {
      flex-wrap: wrap;
      width: 100%;
      border: 1px solid $table-border-color;

      & + tr {
        margin-top: map-get($spacers, 2);
      }
    }

    td {
      flex: 1 1 auto;
      align-items: center;
      width: 100%;
      max-width: 50%;
      min-height: 44px;
      padding: 0 !important;

      &:before {
        content: attr(data-label);
        display: flex;
        align-items: center;
        width: 50%;
        min-height: 44px;
        margin-right: 10px;
        padding: 5px 8px;
        font-weight: bold;
        background-color: map-get($theme-colors, 'light');
      }

      &:not(:nth-child(1)):not(:nth-child(2)) {
        border-top: 1px solid rgba(0,0,0,0.1);

        &:before {
          box-shadow: 0 -1px 0 rgba(0,0,0,0.05);
        }
      }

      > div {
        flex: 1 1 0;
      }

      .btn {
        margin-top: -10px;
        margin-bottom: -10px;
      }
    }

    .tr--input {
      td {
        &:before {
          margin-right: 0;
          border-right: 1px solid rgba($black, 0.05);
        }
      }
    }
  }
}

@include media-breakpoint-down(xs) {
  .table-input {
    td {
      max-width: 100%;

      &:not(:nth-child(1)) {
        border-top: 1px solid rgba(0,0,0,0.1);

        &:before {
          box-shadow: 0 -1px 0 rgba(0,0,0,0.05);
        }
      }
    }
  }
}