.tb__banner {
  position: relative;
  overflow: hidden;
  display: block;

  & img,
  &__image,
  &__overlay,
  &__text,
  &__link {
    display:       block;
    border-radius: inherit;
    transition:    all 0.3s ease-out;
  }
  &__ratio,
  img {
    overflow: hidden;
    display: block;
    height: auto;
    max-width: 100%;
    margin: 0 auto;
  }
  &__ratio--no-maxheight {
    width: 100%;
  }
  & &__overlay,
  & &__text,
  & &__link,
  & &__image {
    width:         100%;
    height:        100%;
  }
  & &__overlay,
  & &__text,
  & &__link,
  &__ratio ~ &__image,
  &__text--static ~ &__ratio,
  &__text--static ~ &__image {
    position:      absolute;
    top:           0;
    right:         0;
    bottom:        0;
    left:          0;
  }
  &__ratio {
    pointer-events: none;
  }
  &__image,
  &__image img {
    background-position: center;
    background-size: cover;
    object-fit: cover;
  }
  &__image--contain,
  &__image--contain img
  {
    object-fit: contain;
  }
  &__image--scaledown,
  &__image--scaledown img
  {
    object-fit: scale-down;
  }
  &__image--top,          &__image--top img          { object-position: top;          }
  &__image--top-right,    &__image--top-right img    { object-position: top right;    }
  &__image--right,        &__image--right img        { object-position: right;        }
  &__image--bottom-right, &__image--bottom-right img { object-position: bottom right; }
  &__image--bottom,       &__image--bottom img       { object-position: bottom;       }
  &__image--bottom-left,  &__image--bottom-left img  { object-position: bottom left;  }
  &__image--left,         &__image--left img         { object-position: left;         }
  &__image--top-left,     &__image--top-left img     { object-position: top left;     }
  &:hover &__image--zoom {
    transform: scale($banner-image-zoom);
  }
  &:not(:hover):not(.active) &__overlay:not(.hover):not(.active) {
    opacity: 0 !important;
  }
  &:hover &__overlay,
  &.active &__overlay,
  &__overlay.active {
    opacity: $banner-overlay-opacity;
  }
  &__text {
    display: flex;
    flex-direction: column;
    pointer-events: none;

    &__line {
      flex: 0 0 auto;

      &.hidden {
        max-height: 0;
        opacity: 0;
        transition: all 0.4s ease-out 0.1s, transform 0.2s ease-out 0s, opacity 0.4s ease-out 0s;
      }
      &.hidden:first-child:last-child {
        max-height: none;
      }
    }
    a, button, input, select, textarea {
      pointer-events: auto;
    }
  }
  & &__text--static {
    position: relative;
  }
  &:hover &__text__line.hidden {
    max-height: 100px;
    opacity: 1;
    transition: all 0.6s ease-out 0s, transform 0.2s ease-out 0.2s, opacity 0.4s ease-out 0.2s;
  }
  &__text__line.hidden ~ &__text__line.hidden {
    transition: all 0.4s ease-out 0s, transform 0.2s ease-out 0.2s, opacity 0.4s ease-out 0.2s;
  }
  &:hover &__text__line.hidden ~ &__text__line.hidden {
    transition: all 0.6s ease-out 0s, transform 0.2s ease-out 0.4s, opacity 0.4s ease-out 0.4s;
  }
  &__text__line.slide-from-top {
    transform: translateY(-20px);
  }
  &__text__line.slide-from-right {
    transform: translateX(20px);
  }
  &__text__line.slide-from-bottom {
    transform: translateY(20px);
  }
  &__text__line.slide-from-left {
    transform: translateX(-20px);
  }
  &:hover &__text__line.slide-from-top,
  &:hover &__text__line.slide-from-right,
  &:hover &__text__line.slide-from-bottom,
  &:hover &__text__line.slide-from-left {
    transform: translateX(0px) translateY(0px);
  }
  &__text__line:nth-last-child(3).hidden,
  &:hover &__text__line:nth-last-child(3).hidden,
  &__text__line:nth-last-child(3).hidden ~ &__text__line.hidden,
  &:hover &__text__line:nth-last-child(3).hidden ~ &__text__line.hidden {
    overflow: visible;
    max-height: none;
  }
  &__link {
    z-index: 1;
    opacity: 0;
  }
}

// Z-index

.tb__banner {
  &__image {
    z-index: 1;
  }
  &__overlay {
    z-index: 2;
  }
  &__link {
    z-index: 3;
  }
  &__text {
    z-index: 4;
  }
}


// Touch device visibility

.is_touch .tb__banner__text__line {
  position: static;
  opacity: 1;
  visibility: visible;
  transform: none;
  max-height: none;
}

// Banner + Lazyloadng image

.tb__banner__image.tb__image:not([style*="padding-top"]) > img,
.tb__banner__image .tb__image__placeholder:not([style*="padding-top"]) > img {
  position: static;
}
