.dropdown.show  > .dropdown-menu,
.dropright.show > .dropdown-menu
{
  display: block !important;
}
.dropdown-menu .dropright > .dropdown-menu {
  margin-top: -#{$dropdown-padding-y}
}

.dropdown-menu {
  color: $dropdown-link-color;

  a:not(.dropdown-item):not(.btn),
  .close
  {
    color: $dropdown-link-color;

    &:not(:disabled):not(.disabled):hover {
      color: $dropdown-link-hover-color;
    }
  }

  .btn-link {
    color: $dropdown-link-hover-color;

    &:hover {
      color: $dropdown-link-color;
    }
  }
  
  .navbar & {
    margin-top: 0;
  }
}

.dropdown-toggle {
  &::after {
    position: relative;
    top: 0.1em;
    opacity: 0.5;
    margin-left: 0.5em;
  }
  &:hover::after {
    opacity: 1;
  }
  &.nocaret::after {
    content: none;
  }
}

.dropdown-menu {
  .dropdown-header {
    padding: $dropdown-header-padding-y $dropdown-header-padding-x;
  }
  .dropdown-title {
    color: $dropdown-title-color;
    font-family: $dropdown-megamenu-title-font;
    font-size: $dropdown-megamenu-title-font-size;
  }
}
.dropdown-menu.w-100w {
  left: calc(50% - 50vw);
}

// Megamenu

.dropdown-megamenu {
  position: static;

  .dropdown-menu {
    width: 100%;
  }
}

// Modal dropdown

.dropdown-modal:before {
  content: '';
  position: fixed;
  z-index:  $zindex-dropdown - 1;
  top:      0;
  right:    0;
  bottom:   0;
  left:     0;
  display:  block;
  background-color: $modal-backdrop-bg;
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;
}
.dropdown-modal.show:before {
  opacity:          $modal-backdrop-opacity;
  pointer-events:   auto;
}
.dropdown-offcanvas {
  overflow: hidden;
  overflow-y: auto;
  z-index: $zindex-fixed;
  position: fixed !important;
  top: 0 !important;
  display: block;
  width: $dropdown-offcanvas-min-width;
  height: 100%;
  max-width: 100%;
  margin: 0;
  transition: all 0.3s;
  transform: none !important;

  .close {
    position: absolute;
    top: $dropdown-padding-y;
    right: $dropdown-padding-y;
    opacity: 1;
  }
  &-right {
    left: auto !important;
    right: 0 !important;
    transform: translateX(110%) !important;
  }
  &-left {
    left: 0 !important;
    right: auto !important;
    transform: translateX(-110%) !important;
  }
  &.show {
    display: flex;
    transform: translateX(0) !important;
  }
}

// Nested dropdowns

.dropdown-menu .dropright .dropdown-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dropright .dropdown-toggle::after,
.dropleft  .dropdown-toggle::after
{
  top: auto;
}

// Menu plugin

.dropright .dropdown-menu,
.dropleft  .dropdown-menu
{
  margin: 0;
}
