.list-group {
  &-borderless &-item {
    border: none;
    border-radius: $list-group-border-radius;
    transition: all 0.3s;
  }
  &-borderless &-item:not(&-item-action),
  &-borderless &-item-action:not(:hover):not(.hover):not(.active)
  {
    padding-left:  0;
    background-color: transparent;
  }
}

// Menu hover

.list-group-item-action {
  .show > & {
    color: $list-group-action-hover-color;
    text-decoration: none;
    background-color: $list-group-hover-bg;
  }
  &:active {
    color: $list-group-action-active-color;
    background-color: $list-group-action-active-bg;
  }
}
.list-group-borderless {
  .show > .list-group-item-action,
  .list-group-item-action.show
  {
    padding-left: $list-group-item-padding-x !important;
  }
}
.list-group-borderless.list-group-sm {
  .show > .list-group-item-action {
    padding-left: $list-group-item-padding-x-sm !important;
  }
}


// Sizes

.list-group-sm {
  .list-group-item {
    @include list-group-size($list-group-item-padding-x-sm, $list-group-item-padding-y-sm);
  }
}
