.nav-item,
.nav-link
{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.nav-link {
  color: $nav-link-color;

  &:not(.disabled):not(.active):hover,
  &:not(.disabled):not(.active).hover
  {
    color: $nav-link-hover-color;
  }
  &:active,
  &.active
  {
    color: $nav-link-active-color;
  }
  > * {
    flex: 0 0 auto;
  }
}

// Nav dark

.nav-dark {
  .nav-link {
    color: $nav-dark-link-color;

    &:not(.disabled):not(.active):hover,
    &:not(.disabled):not(.active).hover
    {
      color: $nav-dark-link-hover-color;
    }
    &:active,
    &.active
    {
      color: $nav-dark-link-active-color;
    }
  }
}

// Nav cart

.nav-cart .dropdown-toggle {
  align-items: flex-end;

  &:after {
    display: none
  }
  .badge {
    position: absolute;
    right: -1px;
    width: 14px;
    height: 14px;
    margin-top: 7px;
    padding: 0;
    line-height: 14px !important;
    font-size: 9px;
    font-weight: normal;
  }
}

// Nav tabs

.nav-tabs {
  .nav-item + .nav-item {
    margin-left: -$nav-tabs-border-width;
  }
  > .nav-link {
    margin-bottom: -$nav-tabs-border-width;
  }
  > .nav-link + .nav-link {
    margin-left: -$nav-tabs-border-width;
  }
  .nav-link.active {
    position: relative;
    z-index: 1;
  }
}

// Nav pills

.nav-pills {
  .nav-link:not(.active):not(.disabled):hover {
    color: $nav-pills-link-hover-color;
    background-color: $nav-pills-link-hover-bg;
  }
}

// Nav underlined

.nav-underlined {
  .nav-link {
    background:
      linear-gradient(0deg, $nav-underlined-link-active-border,  $nav-underlined-link-active-border)  no-repeat #{$nav-link-padding-x} 100% / 0 $nav-underlined-link-border-width;
  }
  .nav-link.active {
    color: $nav-underlined-link-active-color;
    background-size: calc(100% - #{$nav-link-padding-x} * 2) $nav-underlined-link-border-width;
  }
}
@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    .nav-underlined[class*="nav-space#{$infix}"] {
      .nav-link.active {
        background-position: bottom left;
        background-size: 100% $nav-underlined-link-border-width;
      }
    }
  }
}

// Nav responsive

.nav-responsive {
  white-space: nowrap;
  flex-wrap: nowrap;
  overflow-y: hidden;
  overflow-x: auto;
}

// Nav lead

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    .nav-lead > .nav-item > .nav-link {
      padding-top:    map-get($nav-link-lead-padding-y, $breakpoint);
      padding-bottom: map-get($nav-link-lead-padding-y, $breakpoint);
    }
  }
}

// Nav spacing

[class*="nav-space-"]:not(.nav-pills) {
  .nav-link {
    padding-right: 0;
    padding-left:  0;
  }
}
@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @each $key, $value in $nav-spacers {
      .nav-space#{$infix}-#{$key} {
        margin-top:  -#{$value};
        margin-left: -#{$value};

        .nav-item {
          margin-top:  #{$value};
          margin-left: #{$value};
        }
      }
    }
  }
}

// Vertical navigation

.nav.flex-column {
  .nav-link {
    width: 100%;
  }
}

