@if ($btn-borderless > 0) {
  .btn-group {
    .btn + .btn {
      margin-left: 0;
    }
  }
  .btn-group-vertical {
    .btn + .btn {
      margin-top: 0;
    }
  }
}

.btn-group {
  .btn-white + .btn-white {
    margin-left: -#{max($btn-border-width, 1px)};
  }
  .btn-white:not(:first-child):not(:hover):not(:active):not(:focus) {
    border-left-color: rgba(0,0,0,0.02) !important;
  }
  .btn:not(.btn-white):not(:first-child):not(.dropdown-toggle-split) {
    border-left: 1px solid rgba(0,0,0,0.1);
  }
}

// Vertical buttons

.input-group .form-control + .input-group-btn-vertical .btn {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.input-group-btn-vertical .btn:not(:last-child) {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.input-group-btn-vertical .btn:not(:first-child) {
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}

.btn-group.bg-white {
  border: 1px solid transparent;
  background-clip: content-box;

  > * {
    margin-top: -1px;
    margin-bottom: -1px !important;
  }
  > :first-child {
    margin-left: -1px;
  }
  > :last-child {
    margin-right: -1px;
  }
}