// stylelint-disable declaration-no-important

// Min width and height

@each $prop, $abbrev in (min-width: wmin, min-height: hmin, max-width: wmax, max-height: hmax) {
  @each $size, $length in $sizes {
    .#{$abbrev}-#{$size} { #{$prop}: $length !important; }
  }
}

.wmin-0 { min-width:  0 !important; }
.hmin-0 { min-height: 0 !important; }

.wmax-none { max-width:  none !important; }
.hmax-none { max-height: none !important; }