$pixel_base: 16;

@function parseInt($n) {
    @return $n / ($n * 0 + 1); /* 2 */
}

@function strip-unit($n) {
  @if type-of($n) == 'number' and not unitless($n) {
    @return $n / ($n * 0 + 1);
  }

  @return $n;
}

@function rem_to_px($value) {
  $unit:   unit($value);
  $return: strip-unit($value) * $pixel_base;

  @return if($unit == '', $return, $return * 1px);
}

@function px_to_rem($value) {
  $unit:   unit($value);
  $return: strip-unit($value) / $pixel_base;

  @return if($unit == '', $return, $return * 1rem);
}

@function vgrid($font_size) {
  $font_unit: unit($font_size);
  $base_unit: unit($font-size-base);
  $font_size: strip-unit($font_size);
  $baseline:  strip-unit($font-size-base * $line-height-base);

  @if ($font_unit == '') {
    $font_unit: 'px';
  }

  // Rem values
  @if ($font_unit == 'rem' and $base_unit == 'px') {
    $baseline: px_to_rem($baseline);
  }

  // Px values
  @if ($font_unit == 'px' and $base_unit == 'rem') {
    $baseline: rem_to_px($baseline);
  }

  @return #{round($baseline + max(ceil(($font_size - $baseline) / ($baseline / 2)), 0) * ($baseline/2))}#{$font_unit};
}

@function map_last($map, $return: 'value') {
  $length: length($map);

  @each $key, $value in $map {
    @if($length == index(($map), ($key $value))) {
      @if ($return == 'key') {
        @return $key;
      }
      @if ($return == 'value') {
        @return $value;
      }
    }
  }
}

@function map_reverse ($map) {
    $result: null;

    @if type-of($map) == "map" {
        $keys: map-keys($map);
        $map-reversed: ();
        
        @for $i from length($keys) through 1 {
            $map-reversed: map-merge(
                $map-reversed,
                (nth($keys, $i): map-get($map, nth($keys, $i)))
            );
        }

        @if type-of($map-reversed) == "map" {
            $result: $map-reversed;
        } @else {
            @warn 'There was an error reversing the order of "#{$map}"';
        }
    } @else {
        @warn '"#{$map}" is not a valid map';
    }

    @return $result;
}