.navbar-nav > .nav-item > .nav-link {
  text-transform: $navbar-nav-link-transform;
  font-size: $navbar-nav-link-size;
  font-weight: $navbar-nav-link-weight;
  font-family: $navbar-nav-link-family;
}
.navbar-expand {
  @each $breakpoint in map-keys($grid-breakpoints) {
    $next: breakpoint-next($breakpoint, $grid-breakpoints);
    $infix: breakpoint-infix($next, $grid-breakpoints);

    &#{$infix} {
      @include media-breakpoint-down($breakpoint) {
        .nav-item {
          display: block;
        }
        .navbar-collapse,
        .dropdown-menu {
          max-width: 100%;
        }
        .dropdown-menu .dropdown-menu {
          margin: 0 0 0 $dropdown-item-padding-x;
          padding: 0;
          background: transparent none;
        }
        .dropdown-menu:not(.dropdown-offcanvas) {
          display: block !important;
          overflow: hidden;
          transition: all 0.3s;
        }
        .dropdown:not(.show) > .dropdown-menu:not(.dropdown-offcanvas) {
          max-height: 0;
          padding-top: 0 !important;
          padding-bottom: 0 !important;
        }
        .dropdown.show > .dropdown-menu:not(.dropdown-offcanvas) {
          max-height: 500px;
        }
        .dropdown.dropdown-megamenu.show > .dropdown-menu:not(.dropdown-offcanvas) {
          max-height: 2000px;
        }
      }
    }

  }
}

// Navbar toggle icon

.navbar-toggler {
  position: relative;
  width:  $navbar-toggle-button-width;
  height: $navbar-toggle-button-height;
  padding: 0 $navbar-toggle-button-padding-x;
  background-color: transparent !important;
  border: 0 none;
  
  &-icon {
    position: relative;
    top: 0;
    margin-top: -1px;
    background-color: transparent;
    background-image: none;
    transition: opacity 0.07s;

    &,
    &::before,
    &::after {
      display: block;
      height: 1px;
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
      width:  100%;
      background-color: currentColor;
      transition: transform 0.2s ease;
    }
    &::before {
      top: $navbar-toggle-button-height / 2 * -1 + 1;
      transform: translateY($navbar-toggle-button-height / 2 - 1) rotate(45deg);
    }
    &::after {
      bottom: $navbar-toggle-button-height / 2 * -1 + 1;
      transform: translateY($navbar-toggle-button-height / 2 * -1 + 1) rotate(-45deg);
    }
  }
  &.collapsed {
    .navbar-toggler-icon {
      background-image: none;
      background-color: currentColor;
      
      &::before,
      &::after
      {
        transform: none;
      }
    }
  }
  .tb__icon {
    margin-left:  -#{$navbar-toggler-padding-x};
    margin-right: -#{$navbar-toggler-padding-x};
  }
}

// Colors fix

.navbar-dark {
  .navbar-nav {
    .nav-link:not(.active):not(.disabled) {
      @include hover-focus {
        color: $navbar-dark-hover-color;
      }
    }
  }
}
.navbar-light {
  .navbar-nav {
    .nav-link:not(.active):not(.disabled) {
      @include hover-focus {
        color: $navbar-light-hover-color;
      }
    }
  }
}
