.tb__price {
  position: relative;
  display: inline-flex;
  align-items: center;
  line-height: vgrid(1rem);

  &:not(.small) {
    font-size: 26px;
    font-weight: bold;
    line-height: vgrid(1.25rem);
  }
  &__normal,
  &__old,
  &__special
  {
    white-space: nowrap;
  }
  &__decimal-point {
    display: none;
  }
  &__decimal {
    position: relative;
    top: -0.22em;
    margin-left: 0.1em;
    line-height: inherit;
    font-size: 0.6em;
    vertical-align: top;
  }

  &__currency,
  &__period
  {
    font-weight: normal;
    font-size: 0.6em;

    &:not(:first-child) {
      margin-left: 7px;
    }
  }

  &__old {
    position: relative;
    margin-right: 10px;
    font-weight: normal;
    font-size: 0.7em;
    opacity: 0.5;

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: currentColor;
      transform: rotate(-15deg);
      opacity: 0.85;
    }
  }
  &__special {
    text-transform: uppercase;
  }
  &__special.badge {
    font-size: inherit;
    font-weight: inherit;
  }
}
.badge-sticker .tb__price__decimal {
  top: -0.11em;
  line-height: 2.1em;
}
