.top-auto    { top:    auto; }
.right-auto  { right:  auto; }
.bottom-auto { bottom: auto; }
.left-auto   { left:   auto; }
.top-0       { top:    0; }
.right-0     { right:  0; }
.bottom-0    { bottom: 0; }
.left-0      { left:   0; }

.clear-left  { clear: left  !important; }
.clear-right { clear: right !important; }
.clear-both  { clear: both  !important; }

.scroll-x {
  overflow: hidden;
  overflow-x: auto;
}
.scroll-y {
  overflow: hidden;
  overflow-y: auto;
}