.tb__video {
  position: relative;
  overflow: hidden;

  &__text,
  &__cover
  {
    z-index:  2;
    position: absolute;
    top:      0;
    left:     0;
    width:    100%;
    height:   100%;
  }
  &__cover,
  &__cover img
  {
    z-index:  1;
    object-fit: cover;
  }
  &__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    pointer-events: none;
  }
}

// Caption & button

.tb__video {
  &__caption,
  &__button {
    transition: all 0.3s ease-out;
    pointer-events: auto;
  }
  &__button.show:hover {
    transform: scale(1.1);
  }
  &__caption {
    transition-delay: 0.3s;
  }
  &__cover,
  &__cover.banner-image {
    object-fit:       cover;
    transition-delay: 0.6s;
  }
  &__caption:not(.show),
  &__button:not(.show),
  &__cover:not(.show)
  {
    pointer-events: none;
  }
}
