// stylelint-disable declaration-no-important

// Flex variation
//
// Custom styles for additional flex alignment options.

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    .flex#{$infix}-basis-auto  { flex-basis: auto  !important; }
    .flex#{$infix}-basis-0     { flex-basis: 0     !important; }
    .flex#{$infix}-basis-25    { flex-basis: 25%   !important; }
    .flex#{$infix}-basis-50    { flex-basis: 50%   !important; }
    .flex#{$infix}-basis-75    { flex-basis: 75%   !important; }
    .flex#{$infix}-basis-100   { flex-basis: 100%  !important; }
    .flex#{$infix}-basis-25p   { flex-basis: 25px  !important; }
    .flex#{$infix}-basis-50p   { flex-basis: 50px  !important; }
    .flex#{$infix}-basis-75p   { flex-basis: 75px  !important; }
    .flex#{$infix}-basis-100p  { flex-basis: 100px !important; }
    .flex#{$infix}-basis-150p  { flex-basis: 150px !important; }
    .flex#{$infix}-basis-200p  { flex-basis: 200px !important; }
    .flex#{$infix}-basis-250p  { flex-basis: 250px !important; }
    .flex#{$infix}-basis-300p  { flex-basis: 300px !important; }
  }
}
