@import "theme/_functions.scss";
@import "config.scss";

h1, .h1 {
  letter-spacing: -0.5px;
}

#login {
  background: url("../img/login_bg.jpg") no-repeat center / cover;

  h1,
  .lead,
  #copyright
  {
    color: #fff;
  }
}


/* -----------------------------------
   Partials
----------------------------------- */

// Header

.tb__header {
  display: flex;
  width: 100%;
  max-height: 100%;
  padding-top: 138px;
  background: $white;

  .navbar-brand {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 140px;
    height: $baseline * 2;
    max-width: 100%;
    font-size: 26px;
    fill: #fff;

    svg {
      flex-shrink: 0;
      height: 18px;
    }
  }

  hr {
    margin: $baseline * 0.5 25px;
  }

  .nav {
    .nav-item,
    .nav-link
    {
      flex-wrap: nowrap;
      justify-content: center;
    }
    .nav-link {
      font-family: nth($fonts, 1);
      font-size: 15px;
      font-weight: 600;
      color: $white;
    }
  }
}

#header_collapse_button {
  display: none !important;
  position: absolute;
  top: 10px;
  left: calc(100% + 10px);
}

// Default header

@media (min-width: map_get($grid-breakpoints, md)) {
  .tb__header--default {
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    flex-direction: column;
    width: auto;
    height: 100%;

    .navbar {
      display: grid;
      /*grid-template-rows: 90px 1fr;*/
      grid-template-columns: 1fr;
      grid-gap: 0;
      align-items: stretch;
      justify-items: stretch;
      flex: 1 1 0%;
      padding: 0;
      background-color: $white;
      transition: all 0.31s ease-in-out;
    }
    .navbar-brand {
      width: auto;
      height: auto;
      border-left: 5px solid $primary;
    }
    .nav-user {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 60px;
      padding: $spacer * 0.25 $spacer;
      font-size: 15px;
      font-family: nth($fonts, 1);
      color: $primary;
      border-top: 1px solid #ddd;
      border-bottom: 1px solid #ddd;

      .tb__avatar {
        flex-shrink: 0;
      }
    }

    .nav-level-1 {
      display: flex;
      flex-direction: column;
      grid-column: 1;

      .nav {
        width: 100%;

        &:last-child {
          margin-bottom: $spacer;
        }
      }
      .nav-link {
        flex-wrap: nowrap;
        min-height: 58px;

        &:hover,
        &.hover
        {
          color: $white;
          background-color: $hover-bg-link-color;
        }
        &.active,
        &.active:hover
        {
          color: $white;
          background-color: $hover-bg-link-color;
        }
      }
      hr {
        opacity: 0.3;
      }
    }
    .nav-level-2 {
      display: none;
      grid-column: 2;

      .nav-link {
        &:hover {
          color: $primary;
        }
        &.active,
        &.active:hover
        {
          color: #0e2439;
          background-color: #dee4e9;
        }
      }
    }

    &.tb__header--depth-1 {
      .navbar {
        width: 72px;
      }
    }
    &.tb__header--depth-2 {
      .navbar {
        grid-template-columns: 60px 1fr;
        width: 300px;
      }
      .navbar-brand {
        grid-column: 1 / span 2;
        border-left-width: 60px;
      }
      .nav-level-1 {
        color: $white;
        background-color: $primary;

        > .nav > .nav-item > .nav-link,
        > .nav > .nav-item > .nav-link-group > .nav-link
        {
          position: relative;
          flex-wrap: nowrap;
          justify-content: center !important;
          padding-right: 0 !important;
          padding-left: 0 !important;
          color: $white;

          > .tb__icon {
            margin-right: 0 !important;
          }

          &:hover,
          &.hover
          {
            color: rgba($white, 0.8);
            background-color: transparent;
          }
          &.active,
          &.active:hover
          {
            color: $white;
            background-color: rgba($black, 0.17);
          }
        }
        .nav-label {
          z-index: 1000;
          position: absolute;
          overflow: hidden;
          width: 0;
          font-size: 13px;
          opacity: 0;
          pointer-events: none;
          transition: opacity 0.41s ease-in-out;
        }
        .nav-link:hover .nav-label {
          overflow: visible;
          left: calc(100% + 10px);
          width: auto;
          padding: 0 $baseline * 0.3;
          font-weight: normal;
          color: $white;
          background-color: $black;
          border-radius: $border-radius-sm;
          opacity: 1;

          &:before {
            content: '';
            position: absolute;
            right: 100%;
            top: 50%;
            display: block;
            margin-top: -3px;
            border-top: 3px solid transparent;
            border-right: 3px solid $black;
            border-bottom: 3px solid transparent;
          }
        }
      }
      .nav-level-2 {
        display: flex;
        flex-direction: column;
        grid-column: 2;

        .nav-item:last-child {
          margin-bottom: map-get($spacers, 3);
        }
      }
    }
  }
}

// Static (horizontal) header

.tb__header--static {
  background-color: $quinary;

  .navbar-brand {
    .logo_part_2 {
      fill: #fff;
    }
  }
}

// Content

@media (min-width: map_get($grid-breakpoints, sm)) {
  #content {
    min-height: 100vh;
    transition: all 0.3s;
  }
  .tb__header--depth-2 ~ #content {
    margin-left: 300px;
  }
  .tb__header--static ~ #content {
    min-height: auto;
    margin-left: 0;
  }
}

@media (min-width: map_get($grid-breakpoints, md)) {

  .tb__header ~ #content {
    margin-left: 410px;
    margin-right: 460px;
  }
  .tb__header ~ #content.wide {
    margin-right: 120px;
  }

  .tb__header.wider ~ #content {
    margin-left: 280px;
  }

}


/* -----------------------------------
   Components
----------------------------------- */


// Avatar

.tb__avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $baseline * 1.5;
  height: $baseline * 1.5;
  font-size: $h4-font-size;
  font-weight: 800;
  border-radius: 50px;
  transition: all 0.31s;
}


// Breadcrumbs

.breadcrumb {
  font-size: 13px;
}


// Button

.btn {
  cursor: pointer;
}


// Forms

label abbr[title="required"] {
  text-decoration: none;
  color: $danger;
  font-weight: bold;
}

table[data-toggle="autosize"] {
  resize: none;
}

.form-control[size],
.input-group input[size]
{
  width: auto;
  flex: 0 0 auto;
}

.input-group.custom-file {
  display: flex;

  .custom-file-label {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    &:after {
      content: none;
    }
  }

  .custom-file-input {
    width: 0;
    height: 0;
  }
}

.col-form-label {
  margin: 0;
  padding: ($input-height - $baseline) / 2 0 0 0;
}

.checkbox-group {
  padding-top: ($input-height - $baseline) / 2;
  padding-bottom: ($input-height - $baseline) / 2;
}

.checkbox-group--2 {
  columns: 120px 2;
}


// Number

.number {
  &-currency:last-child,
  &-decimal:last-child
  {
    margin-left: 1px;
    line-height: normal;
    font-size: 0.5em;
    vertical-align: top;
  }
}


// Tables

th {
  font-family: nth($fonts, 1);
  font-size: 15px;
}


#customer_data_nav {
  z-index: 1;
  position: absolute;
}

.table-responsive {
  overflow-y: hidden;
}

.table-customer th,
.table-customer td
{
  padding-right: $baseline * 0.75;
  padding-left:  $baseline * 0.75;
}
.table-customer th {
  font-weight: 600;
}
.table-customer th:not(:first-child),
.table-customer td:not(:first-child)
{
  text-align: right;
}
.table-customer thead th {
  background-color: $gray-200;
}
.table-customer thead th:first-child {
}
.table-customer thead th:first-child ~ th {
  border-left: 1px solid $gray-300;
}
.table-customer thead th.th-summary {
  color: $white;
  background-color: $primary;
  border-left: none;
}
.table-customer tbody th:first-child {
  width: 250px;
}
.table-customer tbody tr:first-child th,
.table-customer tbody tr:first-child td
{
  border-top: none;
}
.table-customer tbody tr:first-child th,
.table-customer tbody tr:first-child td
{
  border-top: none;
}
.table-customer tbody td.td-summary {
  background-color: rgba($primary, 0.07);
  border-color: rgba(darken($primary, 15%), 0.1);
}
.table-customer tfoot td {
  padding-top:    $baseline * 0.75 !important;
  padding-bottom: $baseline * 0.75 !important;
  font-size: 18px;
  font-weight: 700;
  font-family: nth($fonts, 1);
}
.table-customer tfoot td:last-child {
  color: $primary;
  font-weight: 600;
}
.table-customer tfoot tr:first-child td {
  padding-top: 10px;
}

// Table Large

.table-lg {
  tbody td {
    // padding-top: $table-cell-padding-lg;
  }
}

// Datagrid

.table-datagrid {
  th.datagrid-header a {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .datagrid-helper {
      display: inline-flex;
      font-family: bullets !important;
      font-weight: normal  !important;
      font-style: normal   !important;
      font-size: 16px;
      transform: rotate(90deg);
      color: $gray-300;

      &:before,
      &:after
      {
        content: '\e904';
        display: flex;
        justify-content: center;
        width: 8px;
      }
      &:before {
        transform: rotate(-180deg);
      }
    }
    .datagrid-helper-asc,
    .datagrid-helper-desc
    {
      margin-right: -2px;
      color: inherit;
      transform: none;
    }
    .datagrid-helper-asc:before,
    .datagrid-helper-desc:before
    {
      content: '\e906';
      font-size: 20px;
    }
    .datagrid-helper-asc:after,
    .datagrid-helper-desc:after
    {
      content: '';
    }
    .datagrid-helper-asc:before {
      transform: rotate(-90deg);
    }
    .datagrid-helper-desc:before {
      transform: rotate(90deg);
    }

    &:hover .datagrid-helper {
      color: inherit;
    }
  }
}

// Options

.options {
  .option {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -15px -15px 0 -15px
  }
  .option-description {
    min-width: 250px;
    flex: 100 1 0;
    margin: 15px 15px 0 15px;
  }
  .option-action {
    display: flex;
    align-items: center;
    flex: 1 1 0;
    min-width: 130px;
    margin: 15px 15px 0 15px;
  }
  .option-action.badge {
    border-radius: $border-radius;
  }
}

// Steps

.steps {
  .nav {
    position: relative;
    justify-content: space-between;

    &:after {
      content: '';
      position: absolute;
      top: 13px;
      right: 1px;
      left: 1px;
      display: block;
      height: 2px;
      background-color: #d0d4d9;
    }
  }
  .nav-link {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: nowrap;
    padding: 0;
    line-height: 1.2;
    font-family: nth($fonts, 1);
    font-weight: 600;
    font-size: 17px;

    &:before {
      content: attr(data-order);
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width:  $baseline * 1.15;
      height: $baseline * 1.15;
      font-size: 15px;
      font-weight: 700;
      border-radius: 50%;
      background-color: #d0d4d9;
      box-shadow: $box-shadow-sm;
    }
    &.passed {
      color: $primary;

      &:before {
        content: '';
        color: $white;
        background-color: $primary;
      }

      .tb__icon {
        position: absolute;
        z-index: 2;
        top: 6px;
        left: 5px;
        width: 18px;
        height: 18px;
        color: #fff;
      }
    }
    &.active {
      color: $tertiary;

      &:before {
        color: $tertiary;
        background-color: $wrapper-bg;
        box-shadow: inset 0 0 0 2px $tertiary;
      }
    }
  }
  .nav-item + .nav-item {
    position: relative;

    &:after {
      content: none;
      position: absolute;
      display: block;
      margin-bottom: 0;
      background-color: rgba(0, 0, 0, 0.15);
    }
  }
  .nav-label {
    display: none;
  }
}

@media (min-width: map_get($grid-breakpoints, 'lg')) {
  .steps {
    .nav {
      flex-direction: column;
      justify-content: stretch;

      &:after {
        content: none;
      }
    }
    .nav-link {
      min-height: $baseline * 1.5;

      &:before {
        width:  $baseline * 1.75;
        height: $baseline * 1.75;
        margin-right: $baseline * 0.75;
        font-size: 19px;
      }

      &.passed {
        .tb__icon {
          top: 10px;
          left: 9px;
          width: 24px;
          height: 24px;
        }
      }
    }
    .nav-item + .nav-item {
      margin-top: $spacer * 1.5;

      &:after {
        content: '';
        top: auto;
        right: auto;
        bottom: 100%;
        left: 20px;
        width: 2px;
        height: $spacer * 1.5;
      }
    }
    .nav-label {
      display: inline;
    }
  }
}

@media (max-width: map_get($grid-breakpoints, 'lg')) {
  .steps {
    z-index: $zindex-sticky;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: $spacer * 0.5 $spacer;
    background: $wrapper-bg;
  }
}

// Wizard

.wizard {
  margin-top: -$spacer;
}
.wizard-step {
  // min-height: 100vh;
  padding-top: $spacer;

  > .card {
    display: block;
  }
}

// Dropzone

.dropzone {
  .dropzone__input {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: map-get($spacers, 3);
    background-color: $light;
    border: 2px dashed darken($light, 20%);
    border-radius: 5px;
    transition: 0.3s ease;
    cursor: pointer;

    .tb__icon, p {
      transition: 0.3s ease;
    }

    &--primary {
      background-color: rgba($primary, 0.10);
      border: 2px dashed rgba($primary, 0.25);

      &:hover {
        background-color: rgba($primary, 0.15);
        border: 2px dashed rgba($primary, 0.40);
      }

      &.dz-drag-hover {
        background-color: rgba($tertiary, 0.10);
        border: 2px dashed rgba($tertiary, 0.25);

        .text-primary {
          color: $tertiary !important;
        }

        .text-primary-dark {
          color: darken($tertiary, 10%) !important;
        }
      }
    }

    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
    }

    input[type="file"] {
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      width: 0;
      height: 0;
      opacity: 0;
      cursor: pointer;
    }
  }
  .dropzone__file-row {
    img {
      background-color: #fff;
    }
    img:not([src]) {
      opacity: 0;
    }
    .content-box {
      box-sizing: content-box;
    }
    &:not(:last-child) {
      margin-bottom: map-get($spacers, 3);
      padding-bottom: map-get($spacers, 3);
      border-bottom: 1px solid lighten($hr-border-color, 10%);
    }
  }
  > .dropzone__file-row {
    display: none;
  }
  .dropzone__queue {
    &:not(:empty) {
      padding-top: map-get($spacers, 3);
    }
  }
}

.activity-log {
  > ul {
    padding: 0;
    list-style: none;
  }

  > p {
    margin-bottom: map-get($spacers, 3);
  }

  > ul + p {
    margin-top: map-get($spacers, 4);
  }
}

.activity-log__record {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  align-content: flex-start;

  &:not(:last-child) {
    padding-bottom: map-get($spacers, 4);
  }
}

.activity-log__time {
  position: relative;
  flex: 0 0 auto;
  margin-right: map-get($spacers, 2);

  @include media-breakpoint-up(md) {
    margin-right: map-get($spacers, 4);
  }

  /*&:before,
  &:after
  {
    content: '';
    position: absolute;
    left: 50%;
    display: block;
    height: 200px;
    border-left: 1px solid $gray-300;
  }

  &:before {
    bottom: calc(100% + 5px);
  }
  &:after {
    top: calc(100% + 8px);
  }*/

  small {
    font-size: 14px;
    color: $activity-time-color;
  }

}

.activity-log__notes {
  flex: 1 1 auto;
  align-self: center;
  padding: 23px;
  background: $secondary-color;
  border-radius: 7px;
  font-size: 16px;
  color: $input-placeholder-color;

  > p:first-child {
    strong  {
      font-size: 16px;
      font-weight: 600;
      font-family: nth($fonts, 1);
    }
  }
}

#sidebar-notes {
  .modal-dialog,
  .modal-content,
  .modal-body
  {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
  }
  .notes {
    flex: 1 1 0;
  }
  .note {
    background-color: $gray-100;
    border-radius: 6px;
  }
  .note-outer {
    background-color: lighten($tertiary, 26%);
  }
  .note + .note {
    margin-top: map-get($spacers, 3);
  }
  textarea {
    max-height: 96px;
  }
}




/* -----------------------------------
   Utilities
----------------------------------- */

.alert-outline-warning {
  color: darken(theme-color('warning'), 35%);
}
