.tb__search {
  position: relative;
  display: flex;

  &__input,
  &__btn
  {
    flex: 1 1 auto;
    min-width: 0;
  }
  &__btn .tb__icon,
  &__input
  {
    transition-delay: 0.6s;
  }
  &__btn {
    position: relative;
    z-index: 2;
  }
  &__btn:not(:hover) {
    color: currentColor !important;
  }
  &__btn .tb__icon {
    transition: all 0.3s 0.6s, color 0s 0s;

    svg {
      transition: all 0.3s 0.6s, color 0s 0s;
    }
  }
  &__input {
    position: absolute !important;
    z-index: 1;
    top: 0;
    right: 0;
    float: none;
    width: 100%;
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    border-width: $search-input-border-width;
    opacity: 0;
    transition: all 0.3s 0.6s;
  }
  &__input--right {
    left: 0;
    right: auto;
    padding-left: $input-btn-height;
  }
  &__input--right.form-control-sm {
    padding-left: $input-btn-height-sm;
  }
  &__input--right.form-control-lg {
    padding-left: $input-btn-height-lg;
  }
  &:hover &__input,
  &:hover &__btn .tb__icon
  {
    transition-delay: 0s;
  }
  &:hover &__input {
    width: 250px;
    opacity: 1;
  }
  &:hover &__btn .tb__icon {
    transform: scale(0.61);

    svg {
      stroke-width: 1px;
      transition-delay: 0s;
    }
  }

  .navbar-light &__input {
    background-color: $input-focus-bg;
  }
}

// Search in Navbar

.nav-link .tb__search {
  display: inline-block;
  margin: -15px -#{$btn-border-width};
  vertical-align: middle;
}
