// stylelint-disable declaration-no-important

// Letter spacing

.space-0 { letter-spacing: 0; }
.space-1 { letter-spacing: 0.1em; }
.space-2 { letter-spacing: 0.2em; }
.space-3 { letter-spacing: 0.3em; }
.space-4 { letter-spacing: 0.4em; }
.space-5 { letter-spacing: 0.5em; }
.space-6 { letter-spacing: 0.6em; }
.space-7 { letter-spacing: 0.7em; }
.space-8 { letter-spacing: 0.8em; }
.space-9 { letter-spacing: 0.9em; }

// Logo

.text-logo {
  display: inline-block;
  flex: 0 0 auto !important;
  padding:        $spacer * 0.125 $spacer * 0.5;
  vertical-align: top;
  text-transform: $logo-text-transform;
  font-family:    $logo-font-family;
  font-size:      $logo-font-size;
  font-weight:    $logo-font-weight;

  &,
  .navbar-brand & {
    margin-top:     $spacer * 0.125;
    margin-bottom:  $spacer * 0.125;
  }
}
@each $color, $value in $theme-colors {
  .text-logo-#{$color} {
    color:  color-yiq($value);
    background: $value;
  }
}
.text-logo:not(:last-child) {
  margin-bottom:  $spacer * 0.625;
}

// Font size

@each $font-size in $font-sizes {
  .font-size-#{$font-size} {
    font-size: #{$font-size}px !important;
    line-height: vgrid($font-size);
  }
}
.smaller  { font-size: $smaller-font-size  !important; }
.smallest { font-size: $smallest-font-size !important; }

// Font weight

.font-weight-100 { font-weight: 100 !important; }
.font-weight-200 { font-weight: 200 !important; }
.font-weight-300 { font-weight: 300 !important; }
.font-weight-400 { font-weight: 400 !important; }
.font-weight-500 { font-weight: 500 !important; }
.font-weight-600 { font-weight: 600 !important; }
.font-weight-700 { font-weight: 700 !important; }
.font-weight-800 { font-weight: 800 !important; }

// Font family

@each $family in $fonts {
  $index: index($fonts, $family);

  .font-custom-#{$index} {
    font-family: #{$family} !important;
  }
}

// Navigation

.text-navigation {
  text-transform: $text-navigation-transform;
  font-family:    $text-navigation-font-family;
  font-size:      $text-navigation-font-size;
  font-weight:    $text-navigation-font-weight;
}

// Text shadow

.text-shadow-light {
  text-shadow: 0 0 10px rgba(0,0,0,0.2);
}
.text-shadow-medium {
  text-shadow: 0 0 20px rgba(0,0,0,0.3);
}
.text-shadow-strong {
  text-shadow: 0 0 20px rgba(0,0,0,0.4);
}
.text-shadow-heavy {
  text-shadow: 0 0 20px rgba(0,0,0,0.5);
}

// Custom mouse pointers

.cursor-help    { cursor: help !important; }
.cursor-pointer { cursor: pointer !important; }