.container-fluid,
.container
{
  flex-wrap: wrap;
}
.container-fluid:before,
.container:before
{
  content: '';
  display: table;
  clear: both;
  flex: 0 0 100%;
  height: 0;
}
.container {
  box-sizing: $container-box-sizing;
  width: auto;
  @if ($container-no-padding) {
    padding-left: 0;
    padding-right: 0;
  }
}
@each $breakpoint, $size in $container-widths {
  .container-#{$breakpoint} { max-width: $size !important; }
}
.row {
  min-width: 100%;
}
.col,
[class*="col-"]
{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  min-width: 0;

  &.align-items-center {
    align-content: center;
  }
  &.align-items-end {
    align-content: flex-end;
  }
  &.align-items-start {
    align-content: flex-start;
  }
}

.col > address,    [class*="col-"] > address,
.col > article,    [class*="col-"] > article,
.col > aside,      [class*="col-"] > aside,
.col > audio,      [class*="col-"] > audio,
.col > blockquote, [class*="col-"] > blockquote,
.col > canvas,     [class*="col-"] > canvas,
.col > div,        [class*="col-"] > div,
.col > dl,         [class*="col-"] > dl,
.col > fieldset,   [class*="col-"] > fieldset,
.col > figure,     [class*="col-"] > figure,
.col > form,       [class*="col-"] > form,
.col > h1,         [class*="col-"] > h1,
.col > h1,         [class*="col-"] > h1,
.col > h2,         [class*="col-"] > h2,
.col > h3,         [class*="col-"] > h3,
.col > h4,         [class*="col-"] > h4,
.col > h5,         [class*="col-"] > h5,
.col > h6,         [class*="col-"] > h6,
.col > hr,         [class*="col-"] > hr,
.col > nav,        [class*="col-"] > nav,
.col > ol,         [class*="col-"] > ol,
.col > p,          [class*="col-"] > p,
.col > pre,        [class*="col-"] > pre,
.col > section,    [class*="col-"] > section,
.col > table,      [class*="col-"] > table,
.col > ul,         [class*="col-"] > ul,
.col > video,      [class*="col-"] > video,
.col > .d-block,   [class*="col-"] > .d-block,
.col > .d-flex,    [class*="col-"] > .d-flex,
.col > .d-grid,    [class*="col-"] > .d-grid,
.col > .d-table,   [class*="col-"] > .d-table
{
  flex: 1 1 100%;
  width: 100%;
}
.col > .d-inline,       [class*="col-"] > .d-inline,
.col > .d-inline-block, [class*="col-"] > .d-inline-block,
.col > .d-inline-flex,  [class*="col-"] > .d-inline-flex,
.col > .d-inline-grid,  [class*="col-"] > .d-inline-grid,
.col > .d-inline-table, [class*="col-"] > .d-inline-table
{
  flex: 0 1 auto;
  width: auto;
}
@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    .col.flex#{$infix}-column > *,   [class*="col-"].flex#{$infix}-column > *,
    .col > .d#{$infix}-inline,       [class*="col-"] > .d#{$infix}-inline,
    .col > .d#{$infix}-inline-block, [class*="col-"] > .d#{$infix}-inline-block,
    .col > .d#{$infix}-inline-flex,  [class*="col-"] > .d#{$infix}-inline-flex
    {
      flex: 0 0 auto;
    }
    .col > .d#{$infix}-block, [class*="col-"] > .d#{$infix}-block,
    .col > .d#{$infix}-table, [class*="col-"] > .d#{$infix}-table,
    .col > .d#{$infix}-flex,  [class*="col-"] > .d#{$infix}-flex
    {
      flex: 1 1 100%;
    }
    .col.flex#{$infix}-column > hr,   [class*="col-"].flex#{$infix}-column > hr
    {
      width: 100%;
    }
  }
}


.col-valign-middle {
  align-items: center;
}

// --------------------------------------
//   GRID GUTTER
// --------------------------------------

.row {
  margin-top:   $grid-default-gutter * -1;
  margin-left:  $grid-default-gutter / -2;
  margin-right: $grid-default-gutter / -2;
}
.col, [class*="col-"] {
  margin-top:    $grid-default-gutter;
  padding-left:  $grid-default-gutter / 2;
  padding-right: $grid-default-gutter / 2;
}
.row.no-gutters {
  margin-top:   0;
  margin-right: 0;
  margin-left:  0;

  > .col,
  > [class*="col-"] {
    margin-top:    0;
    padding-right: 0;
    padding-left:  0;
  }
}

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @each $gutter-breakpoint, $gutter_size in $grid-gutter {
      .row-gut#{$infix}-#{$gutter-breakpoint} {
        margin-top:   $gutter_size * -1;
        margin-left:  $gutter_size / -2;
        margin-right: $gutter_size / -2;
      }
      .row-gutx#{$infix}-#{$gutter-breakpoint} {
        margin-left:  $gutter_size / -2;
        margin-right: $gutter_size / -2;
      }
      .row-guty#{$infix}-#{$gutter-breakpoint} {
        margin-top: $gutter_size * -1;
      }
      .row-gut#{$infix}-#{$gutter-breakpoint} > .col,
      .row-gut#{$infix}-#{$gutter-breakpoint} > [class*="col-"]
      {
        margin-top:    $gutter_size;
        padding-left:  $gutter_size / 2;
        padding-right: $gutter_size / 2;
      }
      .row-gutx#{$infix}-#{$gutter-breakpoint} > .col,
      .row-gutx#{$infix}-#{$gutter-breakpoint} > [class*="col-"]
      {
        padding-left:  $gutter_size / 2;
        padding-right: $gutter_size / 2;
      }
      .row-guty#{$infix}-#{$gutter-breakpoint} > .col,
      .row-guty#{$infix}-#{$gutter-breakpoint} > [class*="col-"]
      {
        margin-top: $gutter_size;
      }
    }
  }
}

// --------------------------------------
//   CUSTOM GRID SIZE
// --------------------------------------

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @each $size, $length in $sizes {
      .col-#{$breakpoint}.w-#{$size} {
        max-width: $length;
      }
    }
  }
}

