.badge {
  line-height: 1 !important;
  font-family: $badge-font-family;
  transition: all 0.3s;
  
  .col > &,
  [class*="col-"] > &
  {
    flex: 0 0 auto;
  }

  div,
  span,
  p {
    line-height: inherit;
  }

  &-xs {
    font-size: 0.5em;
  }
  &-sm {
    font-size: 0.679em;
  }
  &-lg {
    padding: $badge-padding-y * 2 $badge-padding-x * 2;
  }
  &-xl {
    padding: $badge-padding-y * 3 $badge-padding-x * 3;
  }
}

// Sticker badges

.badge {
  &-sticker {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: $input-btn-height;
    height: $input-btn-height;
    padding: 0;
    font-size: 90%;
    border-radius: 150px;

    div,
    span,
    p {
      line-height: 1.3;
    }
  }
  &-sticker.badge-xxs {
    width:  $badge-sticker-xxs;
    height: $badge-sticker-xxs;
    font-size: $badge-sticker-xxs-font-size;
  }
  &-sticker.badge-xs {
    width:  $badge-sticker-xs;
    height: $badge-sticker-xs;
    margin-top: -4px;
    margin-bottom: -4px;
    font-size: $badge-sticker-xs-font-size;
  }
  &-sticker.badge-sm {
    width:  $input-btn-height-sm;
    height: $input-btn-height-sm;
  }
  &-sticker.badge-lg {
    width:  $input-btn-height-lg;
    height: $input-btn-height-lg;
  }
  &-sticker.badge-xl {
    width:  $input-btn-height-xl;
    height: $input-btn-height-xl;
  }
  .dropdown-item & {
    margin-top: -#{$badge-padding-y};
    vertical-align: middle;
  }
}

// Outline badges

.badge {
  &-outline {
    background: transparent;

    &[href]:focus {
      outline: none;
      box-shadow: none;
    }
  }
}
@each $color, $value in $theme-colors {
  .badge-outline.badge-#{$color} {
    color: $value;
    box-shadow: inset 0 0 0 1px $value;

    &[href]:hover {
      color: color-yiq($value);
      background-color: $value;
    }
  }
}

// Subtle color badges

@each $color, $value in $theme-colors {
  .badge-muted.badge-#{$color} {
    color: theme-color-level($color, $badge-muted-color-level);
    background-color: theme-color-level($color, $badge-muted-bg-level);

    &[href]:hover {
      color: color-yiq($value);
      background-color: $value;
    }
  }
}

// Badge with default text size

.badge-normal {
  line-height: inherit;
  font-size: $font-size-base;
}
