@media (min-width: map_get($grid-breakpoints, md)) {
  .alert {
    padding: $alert-padding-md-y $alert-padding-md-x;
  }
}

@each $color, $value in $theme-colors {
  .alert-solid-#{$color} {
    // color: color-yiq(theme-color($color));
    background-color: theme-color($color);

    p {
      color: lighten(theme-color($color), 50%);
    }
  }
  .alert-outline-#{$color} {
    color: theme-color($color);
    background-color: theme-color-level($color, -10);
    box-shadow: inset 0 0 0 $alert-outline-border-width theme-color($color);
  }
}
