.tb__image,
.tb__image > div,
.tb__image > span
{
  position: relative;
  display: flex;
  justify-content: center;
  max-width: 100%;
  font-size: 0;
}
.tb__image {
  img,
  .tb__image__content {
    position: absolute;
    top:      0;
    left:     0;
    width:    100%;
    height:   100%;
  }
  > div,
  > span
  {
    width: 100%;
  }
}

// Image placeholder

.tb__image {
  &__placeholder:before {
    content: '';
    position: absolute;
    top: 0;
    left: auto;
    right: auto;
    display: block;
    width: 52px;
    height: 100%;
    max-width: 25%;
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 487.482 487.482" style="opacity: 0.2;"><path d="M477.482,63.277H10c-5.523,0-10,4.478-10,10v340.928c0,5.522,4.477,10,10,10h467.482c5.522,0,10-4.478,10-10V73.277C487.482,67.755,483.005,63.277,477.482,63.277z M467.482,83.277v235.236l-104.018-80.07c-3.364-2.591-7.996-2.773-11.555-0.457l-68.298,44.456l-134.745-92.417c-3.192-2.189-7.362-2.34-10.706-0.384L20,258.777v-175.5H467.482z M20,404.205V281.948l122.786-71.841l135.024,92.608c3.337,2.286,7.723,2.34,11.111,0.134l67.959-44.235l110.602,85.138v60.453H20z"/><path d="M280.707,218.281c26.063,0,47.266-21.202,47.266-47.264c0-26.058-21.203-47.258-47.266-47.258c-26.062,0-47.264,21.2-47.264,47.258C233.443,197.079,254.645,218.281,280.707,218.281z M280.707,143.76c15.034,0,27.266,12.228,27.266,27.258c0,15.033-12.231,27.264-27.266,27.264c-15.033,0-27.264-12.23-27.264-27.264C253.443,155.987,265.674,143.76,280.707,143.76z"/></svg>');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    pointer-events: none;
    transition: opacity .2s;
  }
  &__placeholder.lazyloaded:before {
    opacity: 0;
  }
}

// Lazyload animations

.tb__image {
  img,
  iframe,
  object
  {
    transition: opacity 0.6s, transform 0.35s;
  }
  img.lazyload:not(.lazyloaded),
  iframe.lazyload:not(.lazyloaded),
  object.lazyload:not(.lazyloaded)
  {
    opacity: 0;
  }
  img.lazyload.slide-from-top:not(.lazyloaded),
  iframe.lazyload.slide-from-top:not(.lazyloaded),
  object.lazyload.slide-from-top:not(.lazyloaded)
  {
    transform: translateY(-40px);
  }
  img.lazyload.slide-from-right:not(.lazyloaded),
  iframe.lazyload.slide-from-right:not(.lazyloaded),
  object.lazyload.slide-from-right:not(.lazyloaded)
  {
    transform: translateX(40px);
  }
  img.lazyload.slide-from-bottom:not(.lazyloaded),
  iframe.lazyload.slide-from-bottom:not(.lazyloaded),
  object.lazyload.slide-from-bottom:not(.lazyloaded)
  {
    transform: translateY(40px);
  }
  img.lazyload.slide-from-left:not(.lazyloaded),
  iframe.lazyload.slide-from-left:not(.lazyloaded),
  object.lazyload.slide-from-left:not(.lazyloaded)
  {
    transform: translateX(-40px);
  }
}

// Border radius

.tb__image * {
  border-radius: inherit;
}

// Figures

.figure {
  // Ensures the caption's text aligns with the image.
  // Allows the figure margin to be set to auto.
  display: table;
}