.tb__countdown {
  display:   flex;
  flex-wrap: wrap;
  padding: $countdown-padding-y $countdown-padding-x;

  & &__label {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 20 1 80px;
    text-align: center;
  }

  & &__time {
    display:     flex;
    flex-wrap:   wrap;
    flex:        80 1 200px;
    align-items: stretch;
    text-align:  center;

    span {
      display:    block;
      flex:       25 1 0px;
      min-width:  50px;
      text-align: center;
      font-size:  $countdown-time-font-size;

      &:after {
        display:    block;
        font-size:  $countdown-time-metric-font-size;
        font-style: $countdown-time-metric-font-style;
        opacity:    0.5;
      }
    }
  }
  &__time {
    span {
      &:after {
        content: ' ' attr(data-label);
      }
    }
    &:not(:last-child) {
      &:after {
        margin-right: 0.6em;
      }
    }
  }
}