.tb__listing {
  clear: both;

  &:before,
  &:after
  {
    content: '';
    display: table;
  }
  &:after {
    clear: both;
  }
  > .row > .col {
    box-sizing: content-box;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: stretch;
    flex: 1 1 auto;
    min-width: 0;
  }
  > .row > .col > * {
    flex: 0 0 auto;
  }
  > .row > .col > .card {
    flex: 1 0 auto;
  }
  > .row > .col:empty {
    display: block !important;
    height: 0;
    min-height: 0;
    margin: 0;
    border-top:    none;
    border-bottom: none;
  }

  // Sizes

  &--50  > .row > .col { width:  50px; }
  &--100 > .row > .col { width: 100px; }
  &--120 > .row > .col { width: 120px; }
  &--140 > .row > .col { width: 140px; }
  &--160 > .row > .col { width: 160px; }
  &--180 > .row > .col { width: 180px; }
  &--200 > .row > .col { width: 200px; }
  &--220 > .row > .col { width: 220px; }
  &--240 > .row > .col { width: 240px; }
  &--250 > .row > .col { width: 250px; }
  &--260 > .row > .col { width: 260px; }
  &--280 > .row > .col { width: 280px; }
  &--300 > .row > .col { width: 300px; }
  &--350 > .row > .col { width: 350px; }
  &--400 > .row > .col { width: 400px; }
  &--450 > .row > .col { width: 450px; }
  &--500 > .row > .col { width: 500px; }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .tb__listing > .row > .col > .card {
    height: 100%;
  }
}