.bootstrap-touchspin {
  .btn {
    width: 1.8em;
    padding: 0;
  }
  .input-group-btn {
    align-items: center;
  }
  .input-group-btn-vertical {
    position: relative;
    display: flex;
    flex-direction: column;
    max-height: 100%;
    margin-left: -1px;

    > .btn {
      display: block;
      flex: 1 1 auto;
      height: auto;
      line-height: 1;

      &[class*="tb__icon"] {
        overflow: hidden;
        text-align: left;
        text-indent: -1000em;
      }
      &:before {
        position: absolute;
        left: 0;
        right: 0;
        display: block;
        width: 100%;
        text-indent: 0;
        text-align: center;
      }
      & + .btn {
        margin-top: -1px;
      }
    }
  }
}
