@keyframes loading-rotate {
  from {transform: rotate(0deg);}
  to   {transform: rotate(359deg);}
}

@mixin loading_size($width: $loading-bar-size) {
  width: $width;
  height: $width;

  .loading > & {
    margin: -#{$width / 2} 0 0 -#{$width / 2};
  }
}

// Loading icon

.tb__loading__bar {
  position: relative;
  transition: all 0.3s;
  transition-delay: 0s;
  pointer-events: none;
  @include loading_size();

  &:before,
  &:after
  {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: block;
    margin: 0;
    border-radius: 50%;
    border-style: solid;
  }
  &:before {
    border-top: $loading-border-width solid;
    border-right: $loading-border-width solid;
    border-bottom: $loading-border-width solid transparent;
    border-left: $loading-border-width solid  transparent;
    border-collapse: collapse;
    opacity: 0.7;
    animation-name: loading-rotate;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  &:after {
    border: $loading-border-width solid;
    opacity: 0.15;
  }
}

// Loading sizes

.tb__loading__bar--sm {
  @include loading_size($loading-bar-size-sm);
}
.tb__loading__bar--lg {
  @include loading_size($loading-bar-size-lg);
}

// Loading block

.tb__loading {
  position: relative;

  &:not(.btn) {
    min-height: $baseline;
  }
  &:not(&--loaded) {
    overflow: hidden;
    pointer-events: none;
  }
  & > * {
    transition: opacity 0.3s;
    opacity: 0;
  }
  &--loaded > * {
    transition-delay: 0.3s;
    opacity: 1;
  }
  & > &__bar {
    z-index: 2;
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 1;
  }
  &--loaded > &__bar {
    opacity: 0;
  }
}

// Listing loading

.tb__listing > .row > .tb__loading {
  min-height: vgrid($pagination-line-height);
}