.modal {
  padding-right: $modal-dialog-margin !important;
  padding-left:  $modal-dialog-margin !important;

  & &-dialog {
    margin-right: auto !important;
    margin-left: auto !important;
  }
}
@include media-breakpoint-up(sm) {
  .modal {
    padding-right: $modal-dialog-margin-y-sm-up !important;
    padding-left:  $modal-dialog-margin-y-sm-up !important;
  }
}

.modal-lg { max-width: $modal-lg; }
.modal-xl { max-width: $modal-xl; }

  @include media-breakpoint-up(xxl) {
  .modal-xxl { max-width: $modal-xxl; }
}

.modal-dialog {
  margin: $modal-dialog-margin !important;
}
@include media-breakpoint-up(sm) {
  .modal-dialog {
    margin: $modal-dialog-margin-y-sm-up auto !important;
  }
}

// Centered modal

.modal-dialog-centered {
  .modal.fade:not(.show) & {
    transform: translate(0, -30px);
  }
}

// Spacing

@media (min-width: map_get($grid-breakpoints, md)) {
  .modal-body {
    padding: $modal-inner-padding-md;
  }
}

// Off canvas

.modal:not(.show) {
  pointer-events: none;
}
.modal-offcanvas {
  display: block !important;
  margin: 0 !important;
  padding: 0 !important;

  .modal-dialog {
    width: $dropdown-offcanvas-min-width;
    max-width: 100%;
    min-height: 100%;
    margin: 0 !important;
    padding: 0;
    background-color: #fff;
    box-shadow: 0 0 30px rgba(0,0,0,0.05);
    transition: all 0.3s;
    transform: none !important;

    .close {
      position: absolute;
      top: $dropdown-padding-y;
      right: $dropdown-padding-y;
      opacity: 1;
    }

    .modal-content,
    .modal-body
    {
      padding: 0;
      border: none;
      border-radius: 0;
      background-color: transparent;
      box-shadow: none;
    }
  }

  &-right .modal-dialog {
    margin-left: auto !important;
    transform: translateX(110%) !important;
  }
  &-left .modal-dialog {
    margin-right: auto !important;
    transform: translateX(-110%) !important;
  }
  &.show .modal-dialog {
    transform: translateX(0) !important;
  }
}
