.tb__icon {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 1em;
  line-height: inherit !important;
  text-align: center;
  vertical-align: top;
  transition: transform 0.3s;

  svg,
  use {
    fill:   currentColor;
    stroke: currentColor;
  }
  svg,
  svg:last-child {
    position: relative;
    margin-top:    -20px;
    margin-bottom: -20px;
    stroke-width: 0px;
  }
  @at-root {
    use[xlink|href*="#feather--"] {
      fill: none;
      stroke-width: 1.4px;
    }
    use[xlink|href*="#social--google-plus"] {
      stroke: none;
    }
  }
  &:before {
    content: '\00a0';
    overflow: hidden;
    display: block;
    width: 0;
    text-indent: -10em;
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
  }
}

// Icon styles

.tb__icon {
  @at-root {
    [class*="tb__icon--stroke"] svg,
    [class*="tb__icon--stroke"] use {
      stroke-width: $icon-linear-stroke-width;
      vector-effect: non-scaling-stroke;
    }
    [class*="tb__icon--stroke"]:not(.tb__icon--fill) svg,
    [class*="tb__icon--stroke"]:not(.tb__icon--fill) use {
      fill: none;
    }
  }
  &--stroke-light svg,
  &--stroke-light use {
    stroke-width: $icon-linear-stroke-width-light;
  }
  &--stroke-heavy svg,
  &--stroke-heavy use {
    stroke-width: $icon-linear-stroke-width-heavy;
  }
  &--stroke-bold svg,
  &--stroke-bold use {
    stroke-width: $icon-linear-stroke-width-bold;
  }
  &--stroke-black svg,
  &--stroke-black use {
    stroke-width: $icon-linear-stroke-width-black;
  }
  &--fill svg,
  &--fill use {
    fill: currentColor;
  }
  &--fill:not([class*="tb__icon--stroke"]) svg,
  &--fill:not([class*="tb__icon--stroke"]) use {
    stroke-width: 0;
  }
}

// Solid icons

.tb__icon {
  &--solid svg,
  &--solid svg:last-child {
    margin-top:    0;
    margin-bottom: 0;
  }
}

// Icon utilities

.tb__icon {
  &--45deg svg {
    transform: rotate(45deg);
  }
  &--90deg svg {
    transform: rotate(90deg);
  }
  &--block {
    display: flex;
    justify-content: center;
  }
  &:hover use * {
    transform: none !important;
  }

  &--chevron-top,
  &--chevron-right,
  &--chevron-bottom,
  &--chevron-left
  {
    font-family: bullets !important;
    font-weight: normal !important;
    font-style:  normal !important;
  }
  &--chevron-top:before,
  &--chevron-right:before,
  &--chevron-bottom:before,
  &--chevron-left:before
  {
    content: '\e904';
  }
  &--chevron-top:before {
    transform: rotate(-90deg);
  }
  &--chevron-bottom:before {
    transform: rotate(90deg);
  }
  &--chevron-left:before {
    transform: rotate(180deg);
  }

  [data-toggle='collapse']:not(.collapsed) &--accordion {
    transform: rotate(90deg);
  }
}
