article,
aside,
footer,
header,
nav,
section {
  display: block;
}
html {
  scroll-behavior: smooth;
}
html,
body
{
  /*
  overflow-x: hidden;
  */
}
body > svg {
  display: none;
}
a {
  transition: all 0.3s;
}
:last-child {
  margin-bottom: 0;
}

input[type="number"][data-toggle="touchspin"] {
  -moz-appearance: none;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button
  {
    -webkit-appearance: none;
    margin: 0;
  }
}

.inactive {
  pointer-events: none !important;
}