@-webkit-keyframes form-control-autofill {
  to {
    color: #666;
    background: $input-bg;
  }
}
@-webkit-keyframes form-control-autofill-focus {
  to {
    color: #666;
    background: $input-focus-bg;
  }
}

.form-control {
  background-clip: border-box;
  transition: all 0.3s;

  &-sm {
    font-size: $input-font-size-sm;
  }
  &-lg {
    font-size: $input-font-size-lg;
  }

  &.disabled,
  &[disabled] {
    color: rgba($input-color, 0.7);
  }
  &:-webkit-autofill {
    -webkit-animation-name: form-control-autofill;
    -webkit-animation-fill-mode: both;
  }
  &:-webkit-autofill:focus {
    -webkit-animation-name: form-control-autofill-focus;
    -webkit-animation-fill-mode: both;
  }
}

.form-control-file::-ms-browse {
  visibility: hidden;
  width: 0;
  padding: 0;
  border: 0;
}
.form-control-file::-webkit-file-upload-button {
  visibility: hidden;
  width: 0;
  padding: 0;
  border: 0;
}

.custom-select {
  background-origin: border-box;
}
.custom-select-sm {
  font-size: $input-font-size-sm;
}
.custom-select-lg {
  font-size: $input-font-size-lg;
}
.custom-select:focus {
  background-color: $custom-select-focus-bg;
}

.custom-control {
  padding-left: calc(#{$custom-control-indicator-size} + 10px) !important;

  &-sm {
    padding-left: calc(#{$custom-control-indicator-size-sm} + 10px) !important;
  }
  &-lg {
    padding-left: calc(#{$custom-control-indicator-size-lg} + 10px) !important;
  }
  &-label {
    display: block;
  }
  &-label::before,
  &-label::after
  {
    left: auto;
    top:          ($baseline - $custom-control-indicator-size) / 2;
    width:        $custom-control-indicator-size;
    height:       $custom-control-indicator-size;
    margin-left:  calc(-10px - #{$custom-control-indicator-size});
    transition:   all 0.3s;
  }
  &-sm &-label::before,
  &-sm &-label::after
  {
    top:          ($baseline - $custom-control-indicator-size-sm) / 2;
    width:        $custom-control-indicator-size-sm;
    height:       $custom-control-indicator-size-sm;
    margin-left:  calc(-10px - #{$custom-control-indicator-size-sm});
  }
  &-lg &-label::before,
  &-lg &-label::after
  {
    top:          ($baseline - $custom-control-indicator-size-lg) / 2;
    width:        $custom-control-indicator-size-lg;
    height:       $custom-control-indicator-size-lg;
    margin-left:  calc(-10px - #{$custom-control-indicator-size-lg});
  }
}

.custom-file-label {
  &::after {

  }
}

//
// Placeholder
//

.form-control::placeholder {
  font-weight: $input-placeholder-font-weight;
}

//
// Validation
//

.form-control.is-valid,
.form-control.is-valid:focus
{
  background-color: lighten($form-feedback-valid-color, $form-feedback-valid-color-bg-strength);

  &::placeholder {
    color: $form-feedback-valid-color;
  }
}
.form-control.is-invalid,
.form-control.is-invalid:focus
{
  background-color: lighten($form-feedback-invalid-color, $form-feedback-invalid-color-bg-strength);

  &::placeholder {
    color: $form-feedback-invalid-color;
  }
}

//
// Toggle
//

@mixin toggle-size($track-width, $track-height, $thumb-width, $thumb-height) {
  &,
  .custom-control-label {
    width:  $track-width;
    height: $track-height;
    line-height: $track-height;
  }
  &,
  &:last-child
  {
    margin-top:    calc((#{vgrid($track-height)} - #{$track-height}) / 2);
    margin-bottom: calc((#{vgrid($track-height)} - #{$track-height}) / 2);
  }
  .custom-control-label {
    &::before {
      width:  calc(#{$thumb-width} - 0.4rem);
      height: calc(#{$thumb-height} - 0.4rem);
      margin: 0.2rem;
    }
  }
  .custom-control-input:checked ~ .custom-control-label {
    &::before {
      left: $track-width - $thumb-width;
    }
  }
}

.custom-toggle {
  display: inline-flex;
  margin: 0;
  padding: 0 !important;
  vertical-align: top;
  @include toggle-size($custom-toggle-track-width, $custom-toggle-track-height, $custom-toggle-thumb-width, $custom-toggle-thumb-height);

  &,
  .custom-control-label {
    min-height: 0;
  }
  .custom-control-label {
    position: relative;
    display: block;
    background-color: $custom-toggle-track-bg !important;
    transition: all 0.3s;
    cursor: pointer;

    &::before {
      top: 0;
      left: 0;
      margin: 0.2rem;
      background-color: $custom-toggle-thumb-bg !important;
      box-shadow: none !important;
    }
  }
  .custom-control-input:checked ~ .custom-control-label {
    background-color: $custom-toggle-track-active-bg !important;

    &::before {
      background-color: $custom-toggle-thumb-active-bg !important;
    }
  }
  .custom-control-input[disabled] ~ .custom-control-label,
  .custom-control-input.disabled ~ .custom-control-label
  {
    background-color: $custom-toggle-track-disabled-bg !important;
    box-shadow: none !important;
    cursor: default;

    &::before {
      background-color: $custom-toggle-thumb-disabled-bg !important;
    }
  }
  .custom-control-input:focus ~ .custom-control-label {
    box-shadow: $custom-toggle-focus-box-shadow;
  }
  .custom-control-input:checked:focus ~ .custom-control-label {
    box-shadow: $custom-toggle-focus-active-box-shadow;
  }
}
.custom-toggle .custom-control-label::after {
  content: none;
}

// Toggle sizes

.custom-toggle-sm {
  @include toggle-size($custom-toggle-track-width-sm, $custom-toggle-track-height-sm, $custom-toggle-thumb-width-sm, $custom-toggle-thumb-height-sm);
}
.custom-toggle-lg {
  @include toggle-size($custom-toggle-track-width-lg, $custom-toggle-track-height-lg, $custom-toggle-thumb-width-lg, $custom-toggle-thumb-height-lg);
}

// ---------------------------
//  Vertical Grid
// ---------------------------

.form-control {
  height:  $input-height;
  padding-top: calc(#{($input-height - $baseline) / 2} - #{$input-border-width});
  padding-bottom: calc(#{($input-height - $baseline) / 2} - #{$input-border-width});

  &-xs {
    height: $input-height-xs;
    padding-top: calc(#{($input-height-xs - $baseline) / 2} - #{$input-border-width-xs});
    padding-bottom: calc(#{($input-height-xs - $baseline) / 2} - #{$input-border-width-xs});
  }
  &-sm {
    height: $input-height-sm;
    padding-top: calc(#{($input-height-sm - $baseline) / 2} - #{$input-border-width-sm});
    padding-bottom: calc(#{($input-height-sm - $baseline) / 2} - #{$input-border-width-sm});
  }
  &-lg {
    height: $input-height-lg;
    padding-top: calc(#{($input-height-lg - $baseline) / 2} - #{$input-border-width});
    padding-bottom: calc(#{($input-height-lg - $baseline) / 2} - #{$input-border-width});
  }
  &[rows] {
    height: auto;
  }
}
select.form-control {
  &:not([size]):not([multiple]) {
    height: $input-height;
  }
  &-sm:not([size]):not([multiple]) {
    height: $input-height-sm;
  }
  &-lg:not([size]):not([multiple]) {
    height: $input-height-lg;
  }
}
.col-form-label {
  padding-top: calc(#{($input-height - $baseline) / 2} - #{$input-btn-border-width});
}