.selectize-control {
  .selectize-input {
    border-bottom-color: transparent !important;
    background-clip: border-box !important;
  }
  .selectize-dropdown {
    overflow: hidden;
    display: flex !important;
    max-height: 0;
    box-shadow: $dropdown-box-shadow !important;
    transition: all 0.31s ease-in-out !important;
    pointer-events: none;

    .selectize-dropdown-content {
      width: 100%;
      box-shadow: none;

      &::-webkit-scrollbar {
        position: relative;
        right: 2px;
        width: 5px;
        margin: 3px;
        background-color: #eee;
      }
      &::-webkit-scrollbar-track {
        background-color: #eee;
        border-radius: 10px;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #ccc;
        border-radius: 10px;
      }
    }
    .option.selected {
      color: $dropdown-link-active-color !important;
      background-color: $dropdown-link-active-bg !important;
    }
  }
  .selectize-input.dropdown-active ~ .selectize-dropdown {
    pointer-events: auto;
    max-height: $selectize-dropdown-height-max;
  }
}