.form-builder {
    min-height: 50vh;
    padding: 10px;
    border: 2px dashed $form-builder-border-2;
    border-radius: 8px;

    &.active {
        border-color: $drag-drop-active;
    }

    &-welcome-text {
        top: 50%;
        width: 100%;
        left: 0;
        text-align: center;
        transform: translateY(-50%);
        font-size: 16px;
        color: $input-placeholder-color;
    }
    
    &-popup {
        height: 100%;
        background: rgba(28, 28, 28, .53);
        z-index: 999;
        overflow: scroll;
        min-height: 100vh;
    }

    &-option {
        p {
            a {
                color: $activity-time-color;
            }
        }
    }

    &-wrap {
        width: 100%;
        max-width: 1280px;
        margin-top: 50px;
        margin-bottom: 50px;
        padding: 30px 20px;
        border-radius: 8px;
        overflow: scroll;
        //min-height: 100%;

        #accordion {
            border-radius: 8px;
            border: 1px solid $form-builder-border;
            overflow: hidden;

                .card {
                    border: none;
                    border-bottom: 1px solid $form-builder-border;
                    border-radius: 0;

                    &:last-child {
                        border-bottom: none;
                    }

                }

                .card-header {
                    .btn {
                        color: $activity-time-color;
                        font-size: 14px;
                        position: relative;
                            &:after {
                                content: "";
                                position: absolute;
                                right: 20px;
                                top: 50%;
                                transform: translateY(-50%);
                                border-top: 10px solid $activity-time-color;
                                border-left: 6px solid transparent;
                                border-right: 6px solid transparent;
                            }

                            &.collapsed {
                                &:after {
                                    border-top: none;
                                    border-bottom: 10px solid $activity-time-color;
                                }
                            }
                    }
                }

                .card-body {
                    padding-top: 0;
                }

        }

        .nav-tabs {
            border-bottom: none;
            .nav-item {
                position: relative;
                .nav-link {
                    font-size: 16px;
                    color: $form-builder-border-2;
                    padding: 6px 33px;
                    border-radius: 0;
                    border-top: 1px solid $form-builder-border-2;
                    border-right: 1px solid $form-builder-border-2;
                    & + a.tb__icon {
                        display: none;
                    }
                    &.active {
                        font-weight: 500;
                        color: $form-builder-active;
                        background: $secondary-color;
                        border-bottom: 1px solid $secondary-color;
                            & + a.tb__icon {
                                display: inline-flex;
                                position: absolute;
                                right: 10px;
                                z-index: 1;
                            }
                    }
                }
                &:first-child .nav-link {
                    border-left: 1px solid $form-builder-border-2;
                    border-radius: 8px 0 0 0;
                }

                &:last-child .nav-link {
                    border-radius: 0 8px 0 0;
                    color: $form-builder-active;
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
        }

        .tab-content {
            max-height: 500px;
            overflow: auto;
            border: 1px solid $form-builder-border-2;
            border-radius: 0 8px 8px 8px;

            &::-webkit-scrollbar {
                width: 12px;
                margin-right: 30px;
            }

            &::-webkit-scrollbar-track {
                border-radius: 9px;
                background: $secondary-color;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 9px;
                background: #CBCBCC;
            }

        }

    }

    .build-form {
        &-row {
            width: 80%;
            padding: 10px;
        }

        .field-label {
            min-width: 80px;
            font-weight: 600;
            margin-right: 20px;
        }

        .field-input {
            flex: 1 0 auto;
            input , &-input , select {
                width: 100%;
                height: 37px;
                margin-bottom: 20px;
                padding-left: 20px;
                box-shadow: 0px 3px 6px #00000014;
                border: 1px solid #CBCBCB;
                border-radius: 7px;
                display: block;
                color: $black;
                font-size: 16px;
                &:focus {
                    outline: none;
                }

                &::placeholder , &::-moz-placeholder {
                    color: $icon-search-color;
                }
            }

            select {
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;       /* Remove default arrow */
                background-image: url(../img/icon-arrow-dropdown.png);   /* Add custom arrow */
                background-repeat: no-repeat;
                background-position: 95% center;
            }

            &-group {
                min-height: 190px;
                padding: 20px;
                border: 2px dashed $form-builder-border-2;
                border-radius: 8px;
                background: $drag-drop-input;

                    .form-builder-welcome-text {
                        padding: 20px;
                        color: $icon-search-color;
                    }

            }

        }

        .custom-checkbox , .custom-radio {
            b {
                font-weight: 400;
            }

            .custom-control-input:checked + span + b {
                font-weight: 500;
            }

            .custom-control-input {
                outline: none;
            }
        }

        .custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
            background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA0AAAAKBAMAAACdwMn3AAAAAXNSR0IB2cksfwAAAB5QTFRFAAAADresC7WtDrirEJ+fELWqDresDrisDbesELOnGp4FaQAAAAp0Uk5TAP9gzxAw4O+fQIZi4AcAAAA7SURBVHicY2AAAhYLBjBwFgwAcw2FGBjCAoBcBQaGxFIwl0FRfDKIy8AkKCgEVqwI5gIFIFwGBgcQAQDtYAU3UckGgwAAAABJRU5ErkJggg==');
            background-size: 12px 10px;
        }

        .custom-radio .custom-control-input:checked ~ .custom-control-label::after {
            background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3Csvg id='Layer_1_1_' style='enable-background:new 0 0 16 16;' fill='%230EB7AC' version='1.1' viewBox='0 0 16 16' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ccircle cx='8' cy='8' r='8'/%3E%3C/svg%3E");
        }

    }

    &-element {
        width: 100%;
        max-width: calc(50% - 4px);
        margin-right: 8px;
        margin-bottom: 6px;
        font-size: 16px;
        font-weight: 600;
        color: $input-placeholder-color;
        padding: 19px 9px 19px 15px;
        border-radius: 8px;
        background: $secondary-color;
        cursor: pointer;

        span.tb__icon {
            margin-right: 18px;
            color: $form-builder-active;
        }

        &:nth-child(2n+2) {
            margin-right: 0;
        }

    }

}