// stylelint-disable declaration-no-important

.pagination {
  border-radius: $pagination-border-radius;

  &:last-child {
    margin-bottom: 0;
  }
  .page-item {
    border-radius: 6px;
    margin: 0 4px;
  }
  .page-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 22px;
    min-width: 22px;
    padding: 0;
    text-align: center;
    border-radius: inherit !important;
    font-size: 16px;
    color: $aside-list-color;
  }
  .page-item.active .page-link {
    z-index: 3;
  }
}
.pagination-sm .page-link {
  height: $pagination-line-height-sm;
  min-width: $pagination-width-sm;
}
.pagination-lg .page-link {
  height: $pagination-line-height-lg;
  min-width: $pagination-width-lg;
}

// Borderless pagination

.pagination-borderless {
  .page-item + .page-item {
    margin-left: $pagination-spacing;
  }
  .page-link {
    margin: 0;
    border-color: transparent !important;
  }
}
.pagination-borderless.pagination-sm {
  .page-item + .page-item {
    margin-left: $pagination-spacing-sm;
  }
}

