// stylelint-disable declaration-no-important

// Reset

.owl-carousel {
  > .hidden {
    display: none;
  }
  button,
  button:hover,
  button:active,
  button:focus
  {
    outline: none;
    box-shadow: none;
  }
  &.show {
    display: block;
  }
  &.owl-loaded {
    .owl-item > * {
      display: block !important;
    }
  }
}

.owl-carousel.row {
  display: flex !important;
  justify-content: center;
  width: auto;
  //align-items: center;

  .owl-stage-outer {
    min-width: 0;
  }
  .owl-item > .col {
    display: flex !important;
    flex-basis: auto;
    width: auto;
    max-width: none;
    margin-top: 0;
    padding-right: 0;
    padding-left: 0;
  }
  &.owl-loaded {
    margin: 0;
  }
}


// Wrapper

.carousel {
  overflow: hidden;
  position: relative;

  .owl-carousel {
    position: static;

    .owl-stage-outer {
      overflow: visible;
    }
  }
}


// Navigaiton

.owl-nav,
.owl-nav button span {
  display: flex;
  align-items: center;
}
.owl-nav {
  overflow: hidden;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  justify-content: space-between;
  width: 100%;
  height: calc(100% - #{$baseline * 2});
  pointer-events: none;
}
.owl-nav button {
  position: relative;
  pointer-events: auto;
  transition: all 0.3s ease-out;
}
.owl-nav button span {
  overflow: hidden;
  justify-content: center;
  width: 50px;
  height: 100px;
  text-indent: -1000em;
  color: $white;
  background: rgba(0,0,0,0.25);
  transition: all 0.3s;
}
.owl-nav button:hover span {
  background: rgba(0,0,0,0.5);
}
.owl-nav button span:before {
  content: '\e907';
  display: block;
  width: 120%;
  text-indent: 0;
  font-family: 'bullets';
  font-size: 42px;
}
.owl-nav .owl-prev,
.owl-nav .owl-next
{
  opacity: 0;
}
.owl-nav .owl-prev,
.owl-nav .owl-prev.disabled
{
  left: -50px;
}
.owl-nav .owl-next,
.owl-nav .owl-next.disabled
{
  right: -50px;
}
.owl-nav .owl-prev span {
  transform: rotate(180deg);
}
.owl-carousel:hover .owl-prev,
.owl-carousel:hover .owl-next
{
  opacity: 1;
}
.owl-carousel:hover .owl-prev:not(.disabled) {
  left: 0;
}
.owl-carousel:hover .owl-next:not(.disabled) {
  right: 0;
}

// Pagination

.owl-dots,
.owl-dot
{
  display: flex;
  height: $baseline;
  align-items: center;
  justify-content: center;
}
.owl-dots {
  clear: both;
  margin-top: $baseline;
  pointer-events: none;
}
.owl-dot {
  width:  $baseline;
  pointer-events: auto;
}
.owl-dot span {
  display: block;
  flex: 0 0 auto;
  width:  10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid transparent;
  background-color: $gray-200;
  background-clip: content-box;
  transition: all 0.3s;
}
.owl-dot.active span {
  background-color: $primary;
  border-color: $primary;
}

// Grid + Carousel

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @each $gutter-breakpoint, $gutter_size in $grid-gutter {
      .row-gut#{$infix}-#{$gutter-breakpoint}  > .owl-stage-outer > .owl-stage > .owl-item > .col,
      .row-gut#{$infix}-#{$gutter-breakpoint}  > .owl-stage-outer > .owl-stage > .owl-item > [class*="col-"],
      .row-gutx#{$infix}-#{$gutter-breakpoint} > .owl-stage-outer > .owl-stage > .owl-item > .col,
      .row-gutx#{$infix}-#{$gutter-breakpoint} > .owl-stage-outer > .owl-stage > .owl-item > [class*="col-"]
      {
        padding-left:  $gutter_size / 2;
        padding-right: $gutter_size / 2;
      }
    }
  }
}
