#wrapper {
  // background-color: $wrapper-bg;
}

.bg-primary,
[class*="bg-dark"]
{
  color: $gray-300;

  a {
    color: $gray-300;

    &:hover {
      color: $white;
    }
  }
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h1 a, .h1 a,
  h2 a, .h2 a,
  h3 a, .h3 a,
  h4 a, .h4 a,
  h5 a, .h5 a,
  .list-arrow li:before,
  .list-chevron li:before,
  .list-triangle li:before
  {
    color: $white;
  }
  h1 a:hover, .h1 a:hover,
  h2 a:hover, .h2 a:hover,
  h3 a:hover, .h3 a:hover,
  h4 a:hover, .h4 a:hover,
  h5 a:hover, .h5 a:hover
  {
    color: $gray-300;
  }
}
.bg-tertiary {
  color: $white;
}
.bg-light {
  .form-control,
  .form-control:hover
  {
    background-color: $white;
  }
}


.bg-primary-90 { background-color: rgba(saturate(darken($primary, 7%), 5%), 0.90); }
.bg-primary-85 { background-color: rgba(saturate(darken($primary, 7%), 5%), 0.85); }
.bg-primary-80 { background-color: rgba(saturate(darken($primary, 7%), 5%), 0.80); }

.bg-transparent {
  background-color: transparent;
}

.animate-bg {
  transition: background 0.3s;
}

.links-gray a:not(:hover) {
  color: $gray-600;
}

// Alternate form input colors

.form-control.form-control-white {
  color: $dark;
  background-color: $white;
  border: $input-border-width solid $white;

  &::placeholder {
    color: lighten($dark, 30%);
  }
}

// Dark bg colors

.text-primary-dark {
  color: darken(theme-color('primary'), 18%) !important;
}
.bg-primary-dark {
  background-color: darken(theme-color('primary'), 18%) !important;
}

// Muted bg colors

@each $color, $value in $theme-colors {
  .bg-muted-#{$color} {
    background-color: theme-color-level($color, $mute-bg-level);
  }
}

// Hover text

@each $color, $value in $theme-colors {
  a.text-hover-#{$color}:hover {
    color: $value !important;
  }
}
// Muted text colors

@each $color, $value in $theme-colors {
  @include text-emphasis-variant(".text-muted-#{$color}", theme-color-level($color, $mute-color-level));
}
