body {
  line-height: $baseline;
}

h1, .h1 { margin-bottom: $h1-margin-bottom; line-height: vgrid($h1-font-size); font-family: $h1-font; font-size: $h1-font-size; font-weight: $h1-font-weight; }
h2, .h2 { margin-bottom: $h2-margin-bottom; line-height: vgrid($h2-font-size); font-family: $h2-font; font-size: $h2-font-size; font-weight: $h2-font-weight; }
h3, .h3 { margin-bottom: $h3-margin-bottom; line-height: vgrid($h3-font-size); font-family: $h3-font; font-size: $h3-font-size; font-weight: $h3-font-weight; }
h4, .h4 { margin-bottom: $h4-margin-bottom; line-height: vgrid($h4-font-size); font-family: $h4-font; font-size: $h4-font-size; font-weight: $h4-font-weight; }
h5, .h5 { margin-bottom: $h5-margin-bottom; line-height: vgrid($h5-font-size); font-family: $h5-font; font-size: $h5-font-size; font-weight: $h5-font-weight; }
h6, .h6 { margin-bottom: $h6-margin-bottom; line-height: vgrid($h6-font-size); font-family: $h6-font; font-size: $h6-font-size; font-weight: $h6-font-weight; }

.display-1 { line-height: vgrid($display1-size); font-family: $display1-font; }
.display-2 { line-height: vgrid($display2-size); font-family: $display2-font; }
.display-3 { line-height: vgrid($display3-size); font-family: $display3-font; }
.display-4 { line-height: vgrid($display4-size); font-family: $display4-font; }

@media (min-width: map_get($grid-breakpoints, 'md')) {
