  h2, .h2 { line-height: vgrid($h2-md-font-size); font-size: $h2-md-font-size; font-weight: $h2-md-font-weight; }
  h3, .h3 { line-height: vgrid($h3-md-font-size); font-size: $h3-md-font-size; font-weight: $h3-md-font-weight; }
  h4, .h4 { line-height: vgrid($h4-md-font-size); font-size: $h4-md-font-size; font-weight: $h4-md-font-weight; }
  h5, .h5 { line-height: vgrid($h5-md-font-size); font-size: $h5-md-font-size; font-weight: $h5-md-font-weight; }
  h6, .h6 { line-height: vgrid($h6-md-font-size); font-size: $h6-md-font-size; font-weight: $h6-md-font-weight; }

  .display-1 { line-height: vgrid($display1-md-size); font-size: $display1-md-size; font-weight: $display1-md-weight; }
  .display-2 { line-height: vgrid($display2-md-size); font-size: $display2-md-size; font-weight: $display2-md-weight; }
  .display-3 { line-height: vgrid($display3-md-size); font-size: $display3-md-size; font-weight: $display3-md-weight; }
  .display-4 { line-height: vgrid($display4-md-size); font-size: $display4-md-size; font-weight: $display4-md-weight; }
}

@media (min-width: map_get($grid-breakpoints, 'lg')) {
  h1, .h1 { margin-bottom: $h1-lg-margin-bottom; line-height: vgrid($h1-lg-font-size); font-size: $h1-lg-font-size; font-weight: $h1-lg-font-weight; }
  h2, .h2 { line-height: vgrid($h2-lg-font-size); font-size: $h2-lg-font-size; font-weight: $h2-lg-font-weight; }
  h3, .h3 { line-height: vgrid($h3-lg-font-size); font-size: $h3-lg-font-size; font-weight: $h3-lg-font-weight; }
  h4, .h4 { line-height: vgrid($h4-lg-font-size); font-size: $h4-lg-font-size; font-weight: $h4-lg-font-weight; }
  h5, .h5 { line-height: vgrid($h5-lg-font-size); font-size: $h5-lg-font-size; font-weight: $h5-lg-font-weight; }
  h6, .h6 { line-height: vgrid($h6-lg-font-size); font-size: $h6-lg-font-size; font-weight: $h6-lg-font-weight; }

  .display-1 { line-height: vgrid($display1-lg-size); font-size: $display1-lg-size; font-weight: $display1-lg-weight; }
  .display-2 { line-height: vgrid($display2-lg-size); font-size: $display2-lg-size; font-weight: $display2-lg-weight; }
  .display-3 { line-height: vgrid($display3-lg-size); font-size: $display3-lg-size; font-weight: $display3-lg-weight; }
  .display-4 { line-height: vgrid($display4-lg-size); font-size: $display4-lg-size; font-weight: $display4-lg-weight; }
