// -------------------------------
//  Address
// -------------------------------

.address {
  display: flex;
  flex-direction: column;
  min-height: $baseline * 5;
  padding-left: $baseline;
  border-left: 5px solid $gray-200;
}

// -------------------------------
//  Blockquote
// -------------------------------

.blockquote {
  line-height: vgrid($blockquote-font-size);
  font-family: $blockquote-font;
  font-weight: $blockquote-font-weight;
  font-style: $blockquote-font-style;

  &-mark {
    display: table;

    &:before {
      display: table-cell;
      content: '\201c';
      width: 1px;
      padding-right: 0.2em;
      line-height: 0.9;
      font-size: 5em;
      vertical-align: top;
      opacity: 0.3;
    }
  }
  &-bordered {
    padding-left: 1.75em;
    border-left: 5px solid $primary;
  }
  &-footer {
    font-family: $blockquote-small-font;
    font-size: $blockquote-small-font-size;
    font-weight: $blockquote-small-font-weight;
    font-style: $blockquote-small-font-style;
  }
}

// -------------------------------
//  Bullet list
// -------------------------------

@font-face {
  font-family: 'bullets';
  src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}

.list-arrow,
.list-triangle,
.list-chevron
{
  padding-left: 0;

  > li {
    position: relative;
    display: table;
    width: 100%;
    table-layout: fixed;

    > a {
      word-break: break-word;
      word-wrap: break-word;
    }
    &:before {
      display: table-cell;
      width: $spacer;
      vertical-align: top;
      font-family: bullets !important;
      font-weight: normal  !important;
      font-style: normal   !important;
    }
  }
}
.list-circle-out > li:before {
  content: '\e900';
}
.list-circle > li:before {
  content: '\e901';
}
.list-close > li:before {
  content: '\e902';
}
.list-triangle > li:before {
  content: '\e903';
}
.list-chevron > li:before {
  content: '\e904';
}
.list-check > li:before {
  content: '\e905';
}
.list-arrow > li:before {
  content: '\e906';
  position: relative;
  top: 0.07em;
  font-size: 0.85em;
}
@each $color, $value in $theme-colors {
  .list-#{$color} > li:before {
    color: $value;
  }
}

// -------------------------------
//  Horizontal ruler
// -------------------------------

@media (min-width: map_get($grid-breakpoints, 'lg')) {
  hr {
    margin-top: $hr-margin-y-lg;
    margin-bottom: $hr-margin-y-lg;
  }
}

// -------------------------------
//  Dropcap
// -------------------------------

.dropcap:first-letter {
  float: left;
  margin-right: 0.25em;
  line-height: $baseline * 3;
  font-size: 5.5em;
}
@each $color, $value in $theme-colors {
  .dropcap-#{$color}:first-letter {
    color: $value;
  }
}
