.input-group {
  &-append.position-absolute {
    z-index: 3;
    right: 0;
  }
}

.input-group-text {
  justify-content: center;
  min-width: $input-height;

  .input-group-sm & {
    min-width: $input-height-sm;
  }
  .input-group-lg & {
    min-width: $input-height-lg;
  }
}

.input-group-append .btn {
  margin-left: -$btn-border-width;
}
.input-group-prepend .btn-white {
  margin-right: -1px;
}
.input-group-append .btn-white {
  margin-left: -1px;
}

// Merged input group

.input-group-merge {
  .form-control {
    order: 2;
    box-shadow: none !important;
  }
  .form-control:not(.form-control-prepend),
  .input-group-append .input-group-text
  {
    border-top-right-radius: $input-border-radius !important;
    border-bottom-right-radius: $input-border-radius !important;
  }
  .form-control:not(.form-control-append),
  .input-group-prepend .input-group-text
  {
    border-top-left-radius: $input-border-radius !important;
    border-bottom-left-radius: $input-border-radius !important;
  }
  .form-control-prepend {
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .form-control-append {
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .input-group-append {
    order: 3;
    margin-left: 0;
    border-radius: 0;
  }
  .input-group-prepend {
    order: 1;
    margin-right: 0;
  }
  .input-group-text {
    min-width: 0;
    background: $input-bg;
    border: $input-border-width solid $input-border-color;
    border-radius: 0;
    transition: all 0.3s;
  }
  .input-group-append .input-group-text {
    border-left: none;
    padding-right: $input-padding-x;
    padding-left: 0;
  }
  .input-group-prepend .input-group-text {
    border-right: none;
    padding-left: $input-padding-x;
    padding-right: 0;
  }
  > .form-control:focus ~ .input-group-prepend .input-group-text,
  > .form-control:focus ~ .input-group-append .input-group-text
  {
    background-color: $input-focus-bg;
    border-color: $input-focus-border-color;
  }
  > .form-control.is-invalid ~ .input-group-prepend .input-group-text,
  > .form-control.is-invalid ~ .input-group-append .input-group-text
  {
    border-color: $form-feedback-invalid-color;
    background-color: lighten($form-feedback-invalid-color, $form-feedback-invalid-color-bg-strength);
  }
  > .form-control.is-invalid ~ .input-group-prepend .input-group-text.text-gray,
  > .form-control.is-invalid ~ .input-group-append .input-group-text.text-gray
  {
    color: rgba($form-feedback-invalid-color, 0.7) !important;
  }
  > :last-child:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    box-shadow: $input-box-shadow;
    border-radius: $input-border-radius;
    pointer-events: none;
    z-index: 5;
  }
  > .form-control:focus ~ :last-child:after {
    box-shadow: $input-focus-box-shadow;
  }
  > .form-control.is-invalid:focus ~ :last-child:after {
    box-shadow: $input-focus-box-shadow;
    box-shadow: 0 0 0 $input-focus-width rgba($form-feedback-invalid-color, .25)
  }

  // Sizes

  // sm

  &.input-group-sm {
    .input-group-append .input-group-text {
      padding-right: $input-padding-x-sm;
    }
    .input-group-prepend .input-group-text {
      padding-left: $input-padding-x-sm;
    }
  }

  &.input-group-lg {
    .input-group-append .input-group-text {
      padding-right: $input-padding-x-lg;
    }
    .input-group-prepend .input-group-text {
      padding-left: $input-padding-x-lg;
    }
  }

}
