.tb__hover {
  overflow: hidden;
  position: relative;
  display: block;
  perspective: 800px;

  &__front,
  &__back
  {
    backface-visibility: hidden;
  }
  &__front {
    position: relative;
    z-index: 2;
  }
  &__back {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    max-width: 100%;
    max-height: 100%;
    margin: 0;
  }
  &:hover &__back,
  &__back.hover
  {
    z-index: 3;
  }
  &:not(:hover) &__back:not(.hover) {
    pointer-events: none;
  }

  // Flip hover

  &__flip-out,
  &__flip-in
  {
    transition: all $hover-flip-duration ease-in-out;
  }
  &__flip-out {
    transform: rotateY(0deg);
  }
  &__flip-in {
    transform: rotateY(-180deg);
  }
  &:hover &__flip-out {
    transform: rotateY(180deg);
  }
  &:hover &__flip-in {
    transform: rotateY(0deg);
  }

  // Opacity (overlay) hover

  &__fade-in,
  &__fade-out
  {
    transition: all $hover-fade-duration ease-out;
  }
  &__fade-in,
  &:hover &__fade-out,
  &__fade-out.hover
  {
    opacity: 0;
  }
  &:hover &__fade-in,
  &__fade-in.hover
  {
    opacity: 1;
  }

  // Slide

  &__slide-in-top,
  &__slide-in-right,
  &__slide-in-bottom,
  &__slide-in-left,
  &__slide-out-top,
  &__slide-out-right,
  &__slide-out-bottom,
  &__slide-out-left
  {
    transition: all $hover-slide-duration cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }
  &__slide-in-top,
  &:hover &__slide-out-top,
  &__slide-out-top.hover
  {
    transform: translateY(-100%);
  }
  &__slide-in-right,
  &:hover &__slide-out-right,
  &__slide-out-right.hover
  {
    left: auto;
    right: 0;
    transform: translateX(100%);
  }
  &__slide-in-bottom,
  &:hover &__slide-out-bottom,
  &__slide-out-bottom.hover
  {
    top: auto;
    bottom: 0;
    transform: translateY(100%);
  }
  &__slide-in-left,
  &:hover &__slide-out-left,
  &__slide-out-left.hover
  {
    transform: translateX(-100%);
  }
  &:hover &__slide-in-top,
  &:hover &__slide-in-right,
  &:hover &__slide-in-bottom,
  &:hover &__slide-in-left,
  &__slide-in-top.hover,
  &__slide-in-right.hover,
  &__slide-in-bottom.hover,
  &__slide-in-left.hover
  {
    transform: translateX(0) translateY(0);
  }
  
  // Delayed hover

  &__delay-out,
  &__delay-in-out,
  &:hover &__delay-in
  {
    transition-delay: $hover-delay;
  }
  &__delay-in,
  &:hover &__delay-out
  {
    transition-delay: 0s;
  }
}

.is_touch .tb__hover__touch-visible {
  z-index:        3;
  pointer-events: auto;
  opacity:        1;
  transform:      none;
}