// Redesign custom styles by @vitalso

/*body {
  font-family: $font-redesign;
}*/

// Top bar styles
.top-bar {
  padding: 10px 48px 10px 34px;
  z-index: 110;
  box-shadow: $box-shadow-sm;

    &-logo {
      img {
        width: 44px;
        height: auto;
      }
    }

    &-user {
      .dropdown-toggle , .dropdown-item {
        font-size: 16px;
      }
      .dropdown-menu {
        width: 180px;
        top: 19px;
        min-width: 0;
          .dropdown-item {
            &:hover {
              background: transparent !important;
            }
          }
      }
    }

}

.top-bar-search , .card-search {
  max-width: 575px;
  padding: 0 15px;
  .form-control {
    height: 38px;
    font-size: 16px;
    color: $input-placeholder-color;
      &::placeholder {
        color: $input-placeholder-color;
      }
  }
  .input-group {
    border: 1px solid $border-grey-color;
    border-radius: 7px;
      &-merge > :last-child:after {
        display: none;
      }
      .tb__icon {
        color: $icon-search-color;
      }
  }
}

// Aside styles
.aside {
  width: 276px;
  display: flex;
  padding-top: 138px;
  left: 72px;

    &-link-list {
      list-style-type: none;
        li {
          margin-bottom: 11px;
        }
        a {
          font-size: 16px;
          line-height: 24px;
          color: $aside-list-color;
            &.current {
              font-weight: $btn-font-weight;
              color: $bottom-line-color;
            }
        }
    }

}

  .aside-padding {
    padding-left: 53px;
    padding-right: 34px;
  }

  .aside-menu-2 {
    ul:first-child {
      border-color: $secondary-color !important;
    }
  }

.dropdown-aside {
  max-width: 190px;
  .dropdown-toggle {
    width: 100%;
    padding-left: 15px;
    color: $input-placeholder-color;
    background: $secondary-color;
    border-radius: 7px;
      &:after {
        opacity: 1;
      }
  }

  .status-badge {
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 15px;
      &.active {
        background: $badge-active-color;
      }
      &.paused {
        background: $badge-paused-color;
      }
  }

  .dropdown-menu {
    width: 100%;
    min-width: 0;
    font-size: 16px;
    color: $input-placeholder-color;
    border-radius: 0 0 7px 7px;
    background: $secondary-color;
      .dropdown-item {
        padding-left: 15px;
        padding-top: 11px;
        padding-bottom: 12px;
        border-top: 1px solid $border-grey-color;
      }
  }

  &.show > .btn.dropdown-toggle {
    color: $input-placeholder-color !important;
    border-radius: 7px 7px 0 0;
    background: $secondary-color !important;
  }

}

#header .nav-level-1 {
  ul {
    li {
      &:last-child {
        margin-top: auto;
      }
    }
  }

  .nav-link {
    .nav-label {
      display: none;
    }
  }
}

// Bottom line for headling
.bottom-line {
  position: relative;
    &:after {
      content: "";
      position: absolute;
      top: calc(100% + 5px);
      left: 0;
      width: 50px;
      height: 3px;
      background: $bottom-line-color;
    }
}

// Card redesign style
.card-redesign {
  //font-size: 16px;
  padding: 25px 30px;
  border-radius: 7px;
  box-shadow: 0px 3px 6px #00000014;

    /*p:not(.number) {
      font-size: 14px;
    }*/

}

  .btn-edit {
    width: 24px;
    height: 24px;
    box-shadow: 0px 3px 6px #00000014;
    border: 1px solid $border-grey-color;
    border-radius: 6px;
    align-content: center;
    justify-content: center;
  }

.list-style-none {
  list-style-type: none;
}

.badge-success {
  background: $badge-active-color;
}

.number {
  font-size: 22px;
  color: $bottom-line-color;
}

.bg-light-redesign {
  background: $secondary-color;
}

// Case side bar styles
.case-sidebar {
  box-shadow: 0px 3px 6px #00000014;
  overflow-y: auto;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  transition: all .3s ease;
  &::-webkit-scrollbar {
    display: none;
  }

  .mobile-case-toggle {
    display: none;
    z-index: 2;
  }

  &-tag {
    .tags {
      .badge {
        margin-right: 7px;
        margin-bottom: 7px;
        padding: 8px 17px;
          &:last-child {
            margin-right: 0;
          }
      }
    }
  }

  &-cards {}

  &.close-out {
    width: 60px !important;
      .hide-out {
        opacity: 0;
      }
  }

  .aside-menu-2 {
    display: none;
  }

}

.filter-sidebar {

  #toggle-sidebar {
    svg:last-child {
      display: none;
    }
  }

  &.close-out {
    #toggle-sidebar {
      svg {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: inline-block;
        }
      }
      &:after {
        display: none;
      }
    }
  }
}

#toggle-sidebar {
  margin-right: 17px;
  &.active {
    background: $bottom-line-color;
    border: none;
    color: $white;
    position: relative;
      &:after {
        content: "";
        position: absolute;
        width: 11px;
        height: 11px;
        border-radius: 50%;
        bottom: -36px;
        left: 50%;
        transform: translateX(-50%);
        background: $badge-active-color;
      }
  }
}

// Edit modal styles
#edit-modal {
  .modal-dialog {
    max-width: 450px;
  }

  input.form-control , select.form-control {
    height: 38px;
    font-size: 16px;
  }

  .btn-primary {
    height: 38px;
  }
}

// Notes modal styles
#note-modal {
  textarea {
    font-size: 16px;
    box-shadow: 0px 3px 6px #00000014;
    background: $textarea-bg;
    border: 1px solid $border-grey-color;

      &::placeholder {
        color: $border-grey-color;
      }

  }

  .btn-primary {
    height: 38px;
  }

}

// Style for custom checkbox
.custom-control-label::before {
  background: $white;
  border: 1px solid $border-grey-color;
  box-shadow: 0px 3px 6px #00000014;
}

// Styles for notes
.notes-row {
  &.system {
    .notes-item {
      margin-left: auto;
    }
    .notes-content {
      color: $white;
      background: $bottom-line-color;
    }
  }

  &.user {
    .notes-content {
      color: $input-placeholder-color;
      background: $secondary-color;
    }
  }
  .notes-item {
    max-width: 590px;
  }
}

// Styles for table
.sort-heading {
  position: relative;
  color: $input-placeholder-color;
    &:after {
      content: "";
      position: absolute;
      top: 8px;
      right: -15px;
      width: 0;
      height: 0;
      border-left: 5px solid transparent;
      border-top: 8px solid $table-head-arrow;
      border-right: 5px solid transparent;
    }
    &.sorted {
      &:after {
        border-top: 8px solid $aside-list-color;
      }
    }
}

// Style for budget table
.table-responsive {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar {
    display: none;
  }
}

.table-budget {
  font-size: 16px;
  overflow-y: hidden;

  thead {
    th {
      vertical-align: middle;
      background: $secondary-color;
      border-bottom: 1px solid $table-rounded-border;
    }
    th:not(:first-child) {
      text-align: center;
      font-size: 16px;
    }
  }

  tbody {
    tr {
      td {
        padding: 28px 14px;
        vertical-align: middle;
        &:first-child {
          font-weight: 600;
        }
        &:not(:first-child) {
          text-align: center;
        }
        &:nth-child(5) {
          background: $textarea-bg;
        }
        &:last-child {
          background: $table-last-column;
        }
      }

      &:hover {
        td {
          &:last-child {
            font-weight: 600;
            background: $textarea-bg;
          }
        }
      }

    }
  }

}

// Fixed first and last column in table
.table-fixed {

  thead , tbody {
    th , td {
      min-width: 139px;

      &:first-child , &:last-child {
        position: sticky;
          &:after {
            content: "";
            width: 1px;
            height: 100%;
            background: $border-grey-color;
            position: absolute;
            top: 0;
          }
      }

      &:first-child {
        left: 0;
        border-left: none;
        background: $white;
          &:after {
            right: 0;
          }
      }

      &:last-child {
        right: 0;
        border-right: none;
          &:after {
            left: 0;
          }
      }
    }
  }

  thead {
    th {
      &:first-child {
        background: $secondary-color;
      }
    }
  }

  tbody tr td:nth-child(5) {
    background: transparent;
  }

}

// Form table
.form-table {
  overflow: hidden;

  p {
    font-size: 16px;
  }

}

// Cases page styles
#header.wider {
  width: 221px;
  padding-top: 84px;

    .cases-header-title {
      display: block;
    }

    .navbar {
      width: 100%;
    }

    .nav-link {
      justify-content: flex-start;
        .nav-label {
          margin-left: 18px;
          display: inline-block;
          font-weight: 400;
        }
    }

}

.cases-header-title {
  margin-bottom: 30px;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 17px;
  font-weight: 600;
  display: none;
  &:after {
    background: $white;
    left: 1rem;
  }
}

@media (min-width: map_get($grid-breakpoints, xl)) {

  #content.wide .container {
    max-width: 1500px;
  }

}

#content .container:nth-child(1) {
  margin-left: 0;
  margin-right: 0;
}

.table-full-width {
  margin: 0 -30px;
  width: calc(100% + 60px);

  th , td {
    &:first-child {
      padding-left: 30px;
    }

    &:last-child {
      padding-right: 30px;
    }
  }

}

.dropdown.active-item {
  display: inline;
    .btn {
      padding-top: 11px;
      padding-bottom: 11px;
    }
}

.scroll-table {
  position: absolute;
  top: 15px;
  z-index: 1;

  &.scroll-left {
    left: 155px;
  }

  &.scroll-right {
    right: 125px;
  }

}

.custom-control-input:checked ~ .custom-control-label::before {
  border-color: $border-grey-color;
  background: $white;
  box-shadow: 0px 3px 6px #00000014;
}

.custom-checkbox .custom-control-label::before , .custom-checkbox .custom-control-label::before {
  outline: none;
}

.document-row {
  display: flex;
}

// Mobile styles

.mobile-topbar {
  padding: 25px 20px;

    .dropdown-aside {
      .dropdown-toggle {
        height: 42px;
        background: $white;
        border: 1px solid $border-grey-color;
        box-shadow: 0px 3px 6px #00000014 !important;
      }
      .dropdown-menu {
        box-shadow: 0px 3px 6px #00000014 !important;
      }
      &.show {
        .dropdown-toggle {
          background: $white !important;
          border-color: transparent !important;
        }
        .dropdown-menu {
          background: $white;
            .dropdown-item {
              padding-top: 8px;
              padding-bottom: 9px;
            }
        }
      }
    }

    .toggle-mobile , .dropdown-aside {
      z-index: 2;
    }

}

.mobile-info {
  width: 42px;
  height: 42px;
  box-shadow: 0px 3px 6px #00000014;
  border: 1px solid $border-grey-color;
}

.mobile-nav {
  left: -100%;
  padding: 120px 20px 20px 20px;
  z-index: 1;
  transition: all .3s ease;
  overflow: auto;
  ul {
    font-size: 22px;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    align-items: center;
      li {
        margin-bottom: 24px;
          &:last-child {
            margin-bottom: 0;
          }
      }
  }

  &.open {
    left: 0;
  }

}

.toggle-mobile {
  .tb__icon {
      svg {
        &:last-child {
          display: none;
        }
      }
    }
  &.open {
    .tb__icon {
      svg {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: inline;
        }
      }
    }
  }
}

.overflow-y {
  overflow-y: hidden;
}

@media screen and (max-width: 1750px) {

  .document-row {
    display: block;
      .card-btn {
        margin-top: 40px;
      }
  }

}

@media (max-width: map_get($grid-breakpoints, md)) {

  html , body {
    overflow-x: hidden;
  }

  h4 {
    font-size: 20px;
  }

  .card-redesign {
    padding-left: 20px;
    padding-right: 20px;

      .card-search.w-50 {
        width: 100% !important;
      }

  }

  // Styles for header
  #header {
    position: fixed;
    z-index: 1;
    bottom: 0;
    padding: 0;
    background: $white !important;

      .navbar , nav , ul {
        width: 100%;
      }

      ul {
        flex-direction: row !important;
        justify-content: space-between;
      }

      .nav {
        .nav-link {
          padding: 0;
          font-size: 14px;
          flex-direction: column;
          font-weight: 400;
          color: $aside-list-color;
            .nav-label {
              margin-top: 5px;
              display: block;
            }
            &.active {
              color: $bottom-line-color;
            }
        }
      }

      &.wider {
        width: 100%;
        padding-top: 0;

          .nav .nav-link .nav-label {
            margin-left: 0;
          }

      }

      &.wider .cases-header-title {
        display: none;
      }

  }

  // Style for content
  #content {
    margin: 0 0 110px 0;
  }

    .mobile-padding {
      padding-left: 20px;
      padding-right: 20px;
    }

  // Styles for case sidebar
  .case-sidebar {
    margin-right: -400px;
      &.open {
        margin-right: 0;
        width: 100% !important;
        padding-top: 20px !important;
        padding-bottom: 80px;
        z-index: 2;
          .hide-out {
            opacity: 1;
          }
      }
      .mobile-case-toggle {
        display: block;
        position: absolute;
        right: 20px;
      }

      .aside-menu-2 {
        display: block;
        ul {
          padding: 15px 20px;
          li {
            margin-bottom: 0;
          }
        }
          .nav-label {
            display: block;
            margin: 0 auto;
          }
      }
  }

}
